import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import ImageStatusComponent from './ImageStatusComponent';
import DashboardComponent from './DashboardComponent';
import FtpComponent from './components/ftp/FtpComponent';
import AddFtpComponent from './components/ftp/AddFtpComponent';
import EmailComponent from './components/ftp/EmailComponent';
import AddEmailComponent from './components/ftp/AddEmailComponent';
import FTPGroupCreateComponent from "./components/ftp/FTPGroupCreateComponent";
import FTPGroupComponent from "./components/ftp/FtpGroupComponent";
import AddEventComponent from "./components/ftp/AddEventComponent";
import EventComponent from "./components/ftp/EventComponent";
import QueueComponent from "./components/ftp/QueueComponent";
import SearchComponent from "./components/ftp/SearchComponent";
import './index.css';
import {Route, Router, hashHistory, IndexRoute} from "react-router";
import PhotoComponent from "./components/ftp/PhotoComponent";
import LoginComponent from "./components/ftp/LoginComponent";
import Dashboardxmp from './components/xmpcreator/Dashboardxmp';
import SquadreComponent from './components/xmpcreator/SquadreComponent';
import AgencyComponent from './components/xmpcreator/AgencyComponent';
import FotografiComponent from './components/xmpcreator/FotografiComponent';
import VenuesComponent from './components/xmpcreator/VenuesComponent';
import CompetitionComponent from './components/xmpcreator/CompetitionComponent';
import SubjectComponent from './components/xmpcreator/SubjectComponent';
import CopyrightComponent from './components/xmpcreator/CopyrightComponent';
import AddFotografiComponent from "./components/xmpcreator/AddFotografiComponent";
import AddAgenziaComponent from "./components/xmpcreator/AddAgenziaComponent";
import AddSquadre from "./components/xmpcreator/AddSquadre";
import AddVenues from "./components/xmpcreator/AddVenues";
import AddCompetition from "./components/xmpcreator/AddCompetition";
import AddSubject from "./components/xmpcreator/AddSubject";
import AddCopyright from "./components/xmpcreator/AddCopyright";
import XmpcaptionComponent from "./components/xmpcreator/XmpcaptionComponent";
import Xmpgenerator1Component from "./components/xmpcreator/Xmpgenerator1Component";
import Dashboardphotographer from './components/photographer/Dashboardphotographer';

ReactDOM.render(
    <Router history={hashHistory}>
        <Route path="/" component={App}>
            <IndexRoute component={DashboardComponent}/>
            <Route path="login" component={LoginComponent}></Route>
            <Route path="status" component={ImageStatusComponent}></Route>
            <Route path="ftp" component={FtpComponent}></Route>
            <Route path="ftp/add" component={AddFtpComponent}></Route>
            <Route path="ftp/edit/:id" component={AddFtpComponent}></Route>
            <Route path="email" component={EmailComponent}></Route>
            <Route path="email/add" component={AddEmailComponent}></Route>
            <Route path="email/edit/:id" component={AddEmailComponent}></Route>
            <Route path="group" component={FTPGroupComponent}></Route>
            <Route path="group/add" component={FTPGroupCreateComponent}></Route>
            <Route path="group/edit/:id" component={FTPGroupCreateComponent}></Route>
            <Route path="event" component={EventComponent}></Route>
            <Route path="event/add" component={AddEventComponent}></Route>
            <Route path="event/edit/:id" component={AddEventComponent}></Route>
            <Route path="event/:code" component={PhotoComponent}></Route>
            <Route path="event/:code/:photo" component={QueueComponent}></Route>
            <Route path="queue" component={QueueComponent}></Route>
            <Route path="xmp" component={Dashboardxmp}></Route>
            <Route path="xmp/Squadre" component={SquadreComponent}></Route>
            <Route path="xmp/Squadre/add" component={AddSquadre}></Route>
            <Route path="xmp/Squadre/edit/:id" component={AddSquadre}></Route>
            <Route path="xmp/Agency" component={AgencyComponent}></Route>
            <Route path="xmp/Agency/add" component={AddAgenziaComponent}></Route>
            <Route path="xmp/Agency/edit/:id" component={AddAgenziaComponent}></Route>
            <Route path="xmp/Fotografi" component={FotografiComponent}></Route>
            <Route path="xmp/Fotografi/add" component={AddFotografiComponent}></Route>
            <Route path="xmp/Fotografi/edit/:id" component={AddFotografiComponent}></Route>
            <Route path="xmp/Venues" component={VenuesComponent}></Route>
            <Route path="xmp/Venues/add" component={AddVenues}></Route>
            <Route path="xmp/Venues/edit/:id" component={AddVenues}></Route>
            <Route path="xmp/Competition" component={CompetitionComponent}></Route>
            <Route path="xmp/Competition/add" component={AddCompetition}></Route>
            <Route path="xmp/Competition/edit/:id" component={AddCompetition}></Route>
            <Route path="xmp/Subject" component={SubjectComponent}></Route>
            <Route path="xmp/Subject/add" component={AddSubject}></Route>
            <Route path="xmp/Subject/edit/:id" component={AddSubject}></Route>
            <Route path="xmp/Copyright" component={CopyrightComponent}></Route>
            <Route path="xmp/Copyright/add" component={AddCopyright}></Route>
            <Route path="xmp/Copyright/edit/:id" component={AddCopyright}></Route>
            <Route path="xmp/Xmpgenerator" component={XmpcaptionComponent}></Route>
            <Route path="xmp/Xmpgenerator1" component={Xmpgenerator1Component}></Route>
            <Route path="photographer" component={Dashboardphotographer}></Route>
            <Route path="search" component={SearchComponent}></Route>



        </Route>
    </Router>,
    document.getElementById('root')
);



