import React, {Component} from "react"
import EventModel from "../../models/EventModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddEventComponent extends Component {
    constructor(props) {
        super(props);
        this.event = new EventModel();
        this.pemail = "";
        this.eventDate = "";
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadEvents= this.loadEvents.bind(this);
        if (props.routeParams.id) {
            this.updateEventsId = props.routeParams.id;
            this.loadEvents(props.routeParams.id);

    }
        this.fetchFtpGroups = this.fetchFtpGroups.bind(this);
        this.state = {
            groups: []
        }
        this.fetchFtpGroups();
    }

    fetchFtpGroups() {
        client.getAllFtpGroups(0, 100000).then(response => {
            this.setState({
                groups: response.content
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching FTP groups. Please reload the page");
        })
    }

    loadEvents(id) {
        client.getEventById(id).then(response => {
            this.pemail = response.pemail;
            this.eventDate = response.eventDate;
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];
                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('event');
        })
    }


    resetValues() {
        Object.keys(this.event).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {

        console.log(this.event);
        Object.keys(this.event).forEach(key => {
            if (key != "destination") {
                if (key != "pemail" && key != "eventDate"){
                    this.event[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
                }
            } else{
                this.event[key].id = ReactDOM.findDOMNode(this.refs[key]).value.trim();
            }
        });
        console.log(this.event);
        this.event.pemail = this.pemail;
        this.event.eventDate = this.eventDate;
        if (this.updateEventsId) {
            this.event["id"] = this.updateEventsId;
        }
        client.createEvent(this.event).then(response => {
            this.props.setSuccess("Event saved successfully");
            //this.resetValues();
            hashHistory.push("event");
        }).catch(e => {
            console.error(e);
            e.response.json().then(response => {
                console.log(response);
                this.props.setError(response.error);
            })
        });

        e.preventDefault();
    }

    render() {
        let groupOptions = [];
        this.state.groups.forEach(grp => {
            groupOptions.push(
                <option value={grp.id}>{grp.name}</option>
            )
        })

        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">Edit Event</h2>
                </div>
                <form onSubmit={this.handleSubmit}>
                        <div className="card">
                            <div className="card-body">
                                <h5 className="card-title">Image Content</h5>
                                <div className="row">
                                    <div className="form-group col-md-4">
                                        <label>Name</label>
                                        <input type="text" ref="name" className="form-control"/>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                        <label>Event Code</label>
                                       <input type="text" ref="eventCode" className="form-control"/>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                        <label>Group</label>
                                        <select ref="destination" className="form-control">
                                            {groupOptions}
                                        </select>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                        <label>Description</label>
                                        <textarea className="form-control" ref="description"/>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                        <label>captionWriter</label>
                                        <input className="form-control" ref="captionWriter"/>
                                     </div>                                    
                                    <div className="form-group col-md-4">
                                        <label>headline</label>
                                       <textarea className="form-control" ref="headline"/>
                                     </div>    
                                     <div className="form-group col-md-4">
                                        <label>subject</label>
                                        <textarea className="form-control" ref="subject"/>
                                    </div>
                                </div>                            
                            </div>
                        </div>
                        <div className="card">
                            <div className="card-body">
                                <h5 class="card-title">Event and Location</h5>
                                <div className="row">
                                    <div className="form-group col-md-4">
                                        <label>city</label>
                                        <input className="form-control" ref="city"/>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                        <label>state</label>
                                        <input className="form-control" ref="state"/>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                        <label>country</label>
                                        <input className="form-control" ref="country"/>
                                    </div>                                    

                                    <div className="form-group col-md-4">
                                    <label>countryCode</label>
                                        <input className="form-control" ref="countryCode"/>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                    <label>location</label>
                                        <input className="form-control" ref="location"/>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                        <label>subjectCode</label>
                                        <input className="form-control" ref="subjectCode"/>
                                    </div>                                    
                                    <div className="form-group col-md-4">
                                    <label>File Name</label>
                                        <textarea type="text" ref="filename" className="form-control"/>
                                    </div>                                    
                                </div>                            
                            </div>
                        </div>
                        <div className="card">
                            <div className="card-body">
                                <h5 class="card-title">Image Rights</h5>
                                <div className="row">
                                    <div className="form-group col-md-4">
                                        <label>creator</label>
                                        <input className="form-control" ref="creator"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>authorsPosition</label>
                                        <input className="form-control" ref="authorsPosition"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>rights</label>
                                        <input className="form-control" ref="rights"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>credit</label>
                                        <input className="form-control" ref="credit"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>source</label>
                                        <input className="form-control" ref="source"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>webStatement</label>
                                        <input className="form-control" ref="webStatement"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>instructions</label>
                                        <textarea className="form-control" ref="instructions"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="card">
                            <div className="card-body">
                                <h5 class="card-title">Status</h5>
                                <div className="row">
                                    <div className="form-group col-md-4">
                                        <label>title</label>
                                        <textarea className="form-control" ref="title"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                    <label>usageTerms</label>
                                        <textarea className="form-control" ref="usageTerms"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>category</label>
                                        <input className="form-control" ref="category"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>subcat</label>
                                        <input className="form-control" ref="subcat"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>subcat1</label>
                                        <input className="form-control" ref="subcat1"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>subcat2</label>
                                        <input className="form-control" ref="subcat2"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="card">
                            <div className="card-body">
                                <h5 class="card-title">Contact Info</h5>
                                <div className="row">
                                    <div className="form-group col-md-4">
                                        <label>ciAdrExtadr</label>
                                        <input className="form-control" ref="ciAdrExtadr"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>ciAdrCity</label>
                                        <input className="form-control" ref="ciAdrCity"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>ciAdrRegion</label>
                                        <input className="form-control" ref="ciAdrRegion"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>ciAdrPcode</label>
                                        <input className="form-control" ref="ciAdrPcode"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>ciAdrCtry</label>
                                        <input className="form-control" ref="ciAdrCtry"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                        <label>ciTelWork</label>
                                        <input className="form-control" ref="ciTelWork"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                    <label>ciEmailWork</label>
                                        <input className="form-control" ref="ciEmailWork"/>
                                    </div>
                                    <div className="form-group col-md-4">
                                    <label>ciUrlWork</label>
                                        <input className="form-control" ref="ciUrlWork"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                <div className="col-md-12">
                    <div className="form-group">
                        <button className="btn btn-secondary">Save</button>
                        <button className="btn btn-danger ml-1" type="reset">Reset</button>
                    </div>
                </div>
            </form>
        </div>    
        );
    }
}

export default AddEventComponent;


