import React, {Component} from "react";

import "./login.css";
import * as ReactDOM from "react-dom";

class LoginComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {};
        this.doLogin = this.doLogin.bind(this);
        this.props.signOut();
    }

    doLogin() {
        var username = ReactDOM.findDOMNode(this.refs["username"]).value;
        var password = ReactDOM.findDOMNode(this.refs["password"]).value;
        this.props.signIn(username + ":" + password);
    }

    render() {

        return (
            <div className="container">
                <div className="wrapper">
                    <form action="#" name="Login_Form" className="form-signin">
                        <h3 className="form-signin-heading">Please Sign In</h3>
                        <hr className="colorgraph"/>
                        <br/>

                        <input type="text" className="form-control" ref="username" placeholder="Username" required=""
                        />
                        <input type="password" className="form-control" ref="password" placeholder="Password"
                               required=""/>

                        <button className="btn btn-lg btn-primary btn-block" name="Submit" value="Login" type="button" onClick={this.doLogin}>
                            Login
                        </button>
                    </form>
                </div>
            </div>
        );
    }
}

export default LoginComponent;


