class FtpModel {
    constructor() {
        this.username = "";
        this.name = "";
        this.password = "";
        this.description = "";
        this.directory = "";
        this.host = "";
        this.createFolder = false;
        this.sftpEnabled = false;

    }
}

export default FtpModel;


