import React, {Component} from "react";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
class QueueComponent extends Component {
    constructor(props) {
        super(props);
        this.fetchQueues = this.fetchQueues.bind(this);
        this.fetchQueueForPhoto = this.fetchQueueForPhoto.bind(this);
        this.callFetch = this.callFetch.bind(this);
        this.fetchPhotosForCode = this.fetchPhotosForCode.bind(this);
        this.state = {
            eventCode: props.routeParams.code,
            photoId: props.routeParams.photo,
            title: "Sending Queue"
        }
        this.callFetch();

    }

    callFetch() {
        if (this.state.photoId) {
            this.fetchPhotosForCode(this.state.eventCode);
            this.fetchQueueForPhoto(this.state.photoId);
        } else {
            this.fetchQueues(0);
        }
    }

    fetchPhotosForCode(code) {
        client.getPhotosForCode(code).then(response => {
            var ids = [];
            response.forEach(p => {
                ids.push(p.id);
            });
            this.setState({
                photoIds: ids
            })
        }).catch(e => {
            console.error(e);
        })
    }

    fetchQueueForPhoto(photoId) {
        client.getQueueForPhoto(photoId).then(response => {
            this.setState({
                content: response
            });
            if (response && response[0]) {
                this.setState({
                    title: "Sending Queue [" + this.state.eventCode + "/" + response[0].photo.fileName + "]"
                })
            }
        }).catch(e => {
            console.error(e);
        });
    }


    fetchQueues(page) {
        client.getAllQueues(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        });
    }

    fetchQueueForNextPhoto() {
        var nextPhotoId = this.hasNextPhoto();
        if (nextPhotoId) {
            this.setState({
                photoId: nextPhotoId
            });
            this.fetchQueueForPhoto(nextPhotoId);
        }
    }

    fetchQueueForPreviousPhoto() {
        var previous = this.hasPreviousPhoto();
        if (previous) {
            this.setState({
                photoId: previous
            });
            this.fetchQueueForPhoto(previous);
        }
    }

    hasNextPhoto() {
        var currentPhotoId = this.state.photoId;
        if (this.state.photoIds && this.state.photoIds.length > 0) {
            var indexOfCurrent = this.state.photoIds.indexOf(parseInt(currentPhotoId));
            if (indexOfCurrent + 1 < this.state.photoIds.length) {
                return this.state.photoIds[indexOfCurrent + 1];
            }
        }
        return false;
    }

    hasPreviousPhoto() {
        var currentPhotoId = this.state.photoId;
        if (this.state.photoIds && this.state.photoIds.length > 0) {
            var indexOfCurrent = this.state.photoIds.indexOf(parseInt(currentPhotoId));
            if (indexOfCurrent - 1 >= 0) {
                return this.state.photoIds[indexOfCurrent - 1];
            }
        }
        return false;
    }

    render() {
        let rows = [];
        let preButtons;
        let nxtButtons;
        if (this.state.content) {
            this.state.content.forEach(queue => {
                rows.push(<tr key={queue.id}>
                    <td>{queue.id}</td>
                    <td className="text-center">
                        <img src={client.getThumbnailUrl(queue.photo.fileName)} width="100"/>
                    </td>
                    <td>{queue.photo.fileName}</td>
                    <td>{queue.ftpAccount.name}</td>
                    <td className={ queue.error ? 'text-danger' : ''}>{queue.error ? 'Error Not Sent' : (queue.processed ? 'Sent' : 'Pending')}</td>
                    <td>{queue.dateAdded ? new Date(queue.dateAdded).toUTCString() : ''}</td>
                    <td>{queue.log}</td>
                </tr>);
            });

            if (this.state.number && this.state.number != 0) {
                preButtons = <button className="btn btn-info navigation-button"
                                     onClick={() => {
                                         this.fetchQueues(this.state.number - 1)
                                     }}>
                    Previous
                </button>;
            }

            if (this.state.last == false) {
                nxtButtons =
                    <button className="btn btn-info navigation-button"
                            onClick={() => {
                                this.fetchQueues(this.state.number + 1)
                            }}>
                        Next
                    </button>;
            }
        }

        let nextPhotoBtn;
        let previousPhotoBtn;
        if (this.hasNextPhoto()) {
            nextPhotoBtn = <button className="btn btn-info" onClick={() => {
                this.fetchQueueForNextPhoto();
            }}>Next
            </button>;
        }

        if (this.hasPreviousPhoto()) {
            previousPhotoBtn = <button className="btn btn-info mr-2" onClick={() => {
                this.fetchQueueForPreviousPhoto();
            }}>Previous
            </button>;
        }


        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">{this.state.title}</h2>
                </div>
                <div className="row">
                    <div className="col-sm-6 text-left">
                        {previousPhotoBtn}
                        {nextPhotoBtn}
                    </div>

                    <div className="col-sm-6 text-right">
                        <button className="btn btn-info" onClick={() => {
                            this.callFetch();
                        }}>Refresh
                        </button>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover" >
                            <thead>
                            <tr>
                                <th>Id</th>
                                <th>Preview</th>
                                <th>Filename</th>
                                <th>Destination</th>
                                <th>Status</th>
                                <th>Date Added</th>
                                <th>Log</th>
                            </tr>
                            </thead>
                            <tbody>
                            {rows}
                            </tbody>
                        </table>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        {preButtons}{nxtButtons}
                    </div>
                </div>
            </div>
        );
    }
}

export default QueueComponent;


