import React, {Component} from "react";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {Link} from "react-router";
class PhotoComponent extends Component {
    constructor(props) {
        super(props);
        this.fetchPhotos = this.fetchPhotos.bind(this);
        this.state = {
            eventCode: props.routeParams.code
        };
        this.fetchPhotos(this.state.eventCode);
    }

    deletePhoto(photoId) {
        client.deletePhoto(photoId).then(response => {
            this.props.setSuccess(photoId + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchPhotos(this.state.eventCode);
        }).catch(e => {
            this.props.setError("Couldn't delete Photo");
        });
    }



    fetchPhotos(code) {
        client.getPhotosForCode(code).then(response => {
            this.setState({content: response});
        }).catch(e => {
            console.error(e);
        })
    }

    render() {
        let rows = [];
        if (this.state.content) {
            this.state.content.forEach(photo => {
                rows.push(<tr key={photo.id}>
                    <td>{photo.id}</td>
                    <Link to={this.props.location.pathname + "/" + photo.id}><img src={client.getThumbnailUrl(photo.fileName)} width="100"/></Link>
                    <td>{photo.fileName}</td>
                    <td>{new Date(photo.dateAdded).toUTCString()}</td>
                    <td className={ photo.error ? 'text-danger' : ''}>{photo.error ? 'Error Occurred' : 'Accepted, and Sent'}</td>
                    <td>{photo.log}</td>
                    <td>
                        <Link to={this.props.location.pathname + "/" + photo.id}>
                            <button className="btn btn-sm btn-primary text-nowrap">View Queue</button>
                        </Link>
                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + photo.id, () => {
                                this.deletePhoto(photo.id);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }

        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">Photos of {this.state.eventCode}</h2>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Id</th>
                                <th>Preview</th>
                                <th>Filename</th>
                                <th>Date Added</th>
                                <th>Status</th>
                                <th>Log</th>
                                <th>Actions</th>
                            </tr>
                            </thead>
                            <tbody>
                            {rows}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}

export default PhotoComponent;


