import React, {Component} from "react"
import FotografiModel from "../../models/FotografiModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddFotografiComponent extends Component {
    constructor(props) {
        super(props);
        this.fotografi = new FotografiModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadFotografi = this.loadFotografi.bind(this);
        if (props.routeParams.id) {
            this.updateFotografiId = props.routeParams.id;
            this.loadFotografi(props.routeParams.id);
        }
    }

    loadFotografi(id) {
        client.getFotografiById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];

                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('fotografi');
        })
    }

    resetValues() {
        Object.keys(this.fotografi).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {

        Object.keys(this.fotografi).forEach(key => {
            this.fotografi[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
        });

        console.log(this.fotografi);
        if (this.updateFotografiId) {
            this.fotografi["id"] = this.updateFotografiId;
        }
        client.createFotografi(this.fotografi).then(response => {
            this.props.setSuccess("Fotografi saved successfully");
            // this.resetValues();
            hashHistory.push("xmp/Fotografi");
        }).catch(e => {
            console.error(e);
            e.response.json().then(response => {
                console.log(response);
                this.props.setError(response.error);
            })
        });

        e.preventDefault();
    }

    render() {
        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">New Photographers</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Photographers Name</label>
                            <input type="text" ref="nome_fotografo" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Photographers Short Name</label>
                            <input type="text" ref="iniziali" className="form-control"/>
                        </div>

                        <div className="form-group">
                         <label>Subject</label>
                         <select ref="authorsPosition" className="form-control">
                        <option value="Staff">Staff</option>
                        <option value="Contributer">Contributer</option>
                        </select>
                        </div>
                        <div className="form-group">
                            <label>Photographers Email</label>
                            <input type="text" ref="email" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Photographers Username</label>
                            <input type="text" ref="username" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Photographers Password</label>
                            <input type="text" ref="password" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary">Save</button>
                            <button className="btn btn-danger ml-1" type="reset">Reset</button>
                        </div>
                    </form>
                </div>
            </div>
    );
}
}

export default AddFotografiComponent;



