import React, {Component} from 'react';
import {hashHistory,Link} from "react-router";
import BackButton from "../../BackButtonComponent"
import {client} from "../../configs";

class SquadreComponent extends Component {
    constructor(props){
        super(props);
        this.deleteSquadre = this.deleteSquadre.bind(this);
        this.fetchSquadres = this.fetchSquadres.bind(this);
        this.state = {};
        this.fetchSquadres(0);

    }
    deleteSquadre(squadre) {
        client.deleteSquadre(squadre).then(response => {
            this.props.setSuccess(squadre.nome_it + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchSquadres(0);
        }).catch(e => {
            this.props.setError("Couldn't delete Squadre");
        });
    }


    fetchSquadres(page) {
        client.getAllSquadre(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }

    render() {
        let Squadres = [];
        let preButtons;
        let nxtButtons;
        if (this.state.content) {
            this.state.content.forEach(squadre => {
                /*let loading = (this.state.onTesting && this.state.onTesting.id == ftpAccount.id) ?
                    <img src="ajax_loader.gif" width={15}/> : '';*/
                Squadres.push(<tr key={squadre.id}>
                    <td>{squadre.nome_it}</td>
                    <td>{squadre.abb_nome}</td>
                    <td>{squadre.subj}</td>
                    <td>{squadre.home_venue}</td>
                    <td>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('xmp/Squadre/edit/' + squadre.id);
                        }}>Edit
                        </button>

                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + squadre.nome_it, () => {
                                this.deleteSquadre(squadre);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
            if (this.state.number && this.state.number != 0) {
                preButtons = <button className="btn btn-info navigation-button"
                                     onClick={() => {
                                         this.fetchSquadres(this.state.number - 1)
                                     }}>
                    Previous
                </button>;
            }

            if (this.state.last == false) {
                nxtButtons =
                    <button className="btn btn-info navigation-button"
                            onClick={() => {
                                this.fetchSquadres(this.state.number + 1)
                            }}>
                        Next
                    </button>;
            }
        }
        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">Teams</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="xmp/Squadre/add">Add New Squadre</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Team Name</th>
                                <th>Team Abbreviation</th>
                                <th>Subject</th>
                                <th>Stadium</th>
                                <th>Action</th>
                            </tr>
                            </thead>
                            <tbody>
                            {Squadres}
                            </tbody>
                        </table>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        {preButtons}{nxtButtons}
                    </div>
                </div>
            </div>
        );
    }
}

export default SquadreComponent;



