import React, {Component} from 'react';
import {hashHistory,Link} from "react-router";
import BackButton from "../../BackButtonComponent"
import {client} from "../../configs";

class AgencyComponent extends Component {
    constructor(props){
        super(props);
         this.deleteAgency = this.deleteAgency.bind(this);
        this.fetchAgencies = this.fetchAgencies.bind(this);
         this.state = {};
        this.fetchAgencies(0);
       
    }
     deleteAgency(agency) {
        client.deleteAgency(agency).then(response => {
            this.props.setSuccess(agency.nome_agenzia + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchAgencies(0);
        }).catch(e => {
            this.props.setError("Couldn't delete Agency.Please try agin.");
        });
    }


    fetchAgencies(page) {
        client.getAllAgency(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }

    render() {
         let agencies = [];
        if (this.state.content) {
            this.state.content.forEach(agency => {
                /*let loading = (this.state.onTesting && this.state.onTesting.id == ftpAccount.id) ?
                    <img src="ajax_loader.gif" width={15}/> : '';*/
                agencies.push(<tr key={agency.aid}>
                    <td>{agency.nome_agenzia}</td>
                    <td>{agency.indirizzo}</td>
                    <td>{agency.provincia}</td>
                    <td>{agency.citta}</td>
                    <td>{agency.cap}</td>
                    <td>{agency.nazione}</td>
                    <td>{agency.telefono}</td>
                    <td>{agency.email}</td>
                    <td>{agency.sito}</td>
                    <td className="text-nowrap">
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('xmp/Agency/edit/' + agency.aid);
                        }}>Edit
                        </button>
                       
                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + agency.nome_agenzia, () => {
                                this.deleteAgency(agency);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }
        return (
            <div>
                <div className="row">
                    <BackButton location="xmp"/>
                    <h2 className="page-title">Agency</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="/xmp/Agency/add">Add New Agency</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Agency</th>
                                <th>Address</th>
                                <th>City</th>
                                <th>State</th>
                                <th>Zip</th>
                                <th>Country</th>
                                <th>Phone</th>
                                <th>E-mail</th>
                                <th>Web Site</th>
                                <th>Action</th>
                            </tr>
                            </thead>
                            <tbody>
                            {agencies}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}
export default AgencyComponent;


