import React, {Component} from "react";
import IconComponent from "../../IconComponent";
import {client} from "../../configs";

class DashboardComponent extends Component {

    constructor(props) {
        super(props);
        client.ping();
    }

    render() {
        return (
            <div>
                <div className="row">
                    <IconComponent to="/xmp/Agency" title="Agency" ico="Agency.png"/>
                    <IconComponent to="/xmp/Fotografi" title="Photographers" ico="icophoto.png"/>
                    <IconComponent to="/xmp/Squadre" title="Squadre" ico="footballer-icon.png"/>
                    <IconComponent to="/xmp/Venues" title="Venues" ico="stadium-icon.png"/>
                    <IconComponent to="/xmp/Competition" title="Competition" ico="ball-trophy-icon.png"/>
                    <IconComponent to="/xmp/Subject" title="Subject" ico="subject.png"/>
                    <IconComponent to="/xmp/Copyright" title="Copiyright" ico="copyright-icon.png"/>
                    <IconComponent to="/xmp/Xmpgenerator" title="XMP Generator" ico="xmp-icon.png"/>
                </div>
            </div>

        )
    }
}

export default DashboardComponent;


