import React, {Component} from "react";
import CompetitionModel from "../../models/CompetitionModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";
import "../../checkbox.css";

import PropTypes from 'prop-types';
import Checkbox from './checkbox';




class AddCompetition extends Component {
    constructor(props) {
        super(props);
        this.competition = new CompetitionModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadCompetition = this.loadCompetition.bind(this);
        this.fetchSquadres = this.fetchSquadres.bind(this);
        this.handleChange = this.handleChange.bind(this);

        //edit Compitition
        if (props.routeParams.id) {
            this.updateCompetitionId = props.routeParams.id;
            this.loadCompetition(props.routeParams.id);
        }

        this.fetchSubjectGroups = this.fetchSubjectGroups.bind(this);
        this.fetchSubjectGroups();
        this.state = {
            subjectGroups: [],
            checkedItems: new Map(),
        }



    }
    componentDidMount(){
        this.fetchSquadres(0,10000);
    }


    handleChange(e) {
        const item = e.target.name;
        const isChecked = e.target.checked;
        this.setState(prevState => ({ checkedItems: prevState.checkedItems.set(item, isChecked) }));
    }
    fetchSubjectGroups() {
        client.getAllSubjectGroups(0, 100000).then(response => {
            this.setState({
                subjectGroups: response.content
            });
            if(this.state.subjectGroups.length > 0) {
                this.fetchCompetitionGroups(this.state.subjectGroups[0].subject);
            }
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Subject groups. Please reload the page");
        })
    }

    loadCompetition(id) {
        client.getCompetitionById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];

                }

            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('competition');
        })
    }

    fetchSquadres(page, pageSize) {
        client.getAllSquadreNopaging(page, pageSize).then(response => {
            this.setState(response);
            if (this.updateCompetitionId) {
                client.getCompetitionById(this.updateCompetitionId).then(response => {
                    if (response.squadreList) {
                        response.squadreList.forEach(keyItem => {
                            const item = keyItem.nome_it;
                            const isChecked = true;
                            this.setState(prevState => ({checkedItems: prevState.checkedItems.set(item, isChecked)}));
                        });
                    }
                }).catch(e => {
                    this.props.setError("Update failed. Please retry");
                    hashHistory.push('competition');
                });
            }

        }).catch(e => {
            console.error(e);
        })
    }

    resetValues() {
        Object.keys(this.competition).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
        console.log("Before clear "+this.state.checkedItems);
        this.state.checkedItems.clear();
        console.log("After clear "+this.state.checkedItems);
    }

    handleSubmit(e) {
        Object.keys(this.competition).forEach(key => {
            this.competition[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
        });


        if (this.updateCompetitionId) {
            this.competition["id"] = this.updateCompetitionId;
        }
        //add teams
        var teams=[];
        var keys=this.state.checkedItems.keys();
        for(var mapKey of keys){
            if(this.state.checkedItems.get(mapKey)==true){
                console.log(mapKey);
                    teams.push(mapKey);
            }
        };
        this.competition.teams=teams;
        client.createCompetition(this.competition).then(response => {
            this.props.setSuccess("Competition saved successfully");
            // this.resetValues();
            hashHistory.push("xmp/Competition");
        }).catch(e => {
            console.error(e);
            e.response.json().then(response => {
                console.log(response);
                this.props.setError(response.error);
            })
        });

        e.preventDefault();
    }

    render() {

        let subjectOptions = [];
        var teams=[];
        if(this.state.content){
            teams=this.state.content;
        }
        this.state.subjectGroups.forEach(sub => {
            subjectOptions.push(
                <option key={sub.aid} value={sub.subject}>{sub.subject}</option>
            )
        })
        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">Add Competition</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Competition</label>
                            <input type="text" ref="nome_it" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Subject</label>
                            <select ref="subject" className="form-control">
                                {subjectOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Subject Abbreviation</label>
                            <input type="text" ref="subjectabb" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Keywords</label>
                            <input type="text" ref="keyword" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Select Teams</label>
                            <div className="checkborder">
                            {
                                teams.map(item => (
                                    <div className="checkbox">
                                        <label>
                                            <Checkbox name={item.nome_it}
                                                      checked={this.state.checkedItems.get(item.nome_it)}
                                                      onChange={this.handleChange} className="form-control"/>
                                            <span className="tick">{item.nome_it}</span>
                                        </label>
                                    </div>
                                ))

                            }
                            </div>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary">Save</button>
                            <button className="btn btn-danger ml-1" type="reset" onClick={()=>{this.state.checkedItems.clear()}}>Reset</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default AddCompetition;



