import React, {Component} from 'react';
import {hashHistory,Link} from "react-router";
import BackButton from "../../BackButtonComponent"
import {client} from "../../configs";

class SubjectComponent extends Component {
    constructor(props){
        super(props);
        this.deleteSubject = this.deleteSubject.bind(this);
        this.fetchSubjects = this.fetchSubjects.bind(this);
        this.state = {};
        this.fetchSubjects(0);

    }
    deleteSubject(subject) {
        client.deleteSubject(subject).then(response => {
            this.props.setSuccess(subject.codice + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchSubjects(0);
        }).catch(e => {
            this.props.setError("Couldn't delete Subject");
        });
    }


    fetchSubjects(page) {
        client.getAllSubject(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }
    render() {
        let subjects = [];
        if (this.state.content) {
            this.state.content.forEach(subject => {
                /*let loading = (this.state.onTesting && this.state.onTesting.id == ftpAccount.id) ?
                    <img src="ajax_loader.gif" width={15}/> : '';*/
                subjects.push(<tr key={subject.id}>
                    <td>{subject.codice}</td>
                    <td>{subject.subject}</td>
                    <td>{subject.categoria}</td>
                    <td>{subject.afpcat}</td>
                    <td>{subject.subcat}</td>
                    <td>{subject.subcat1}</td>
                    <td>{subject.subcat2}</td>
                    <td>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('xmp/Subject/edit/' + subject.aid);
                        }}>Edit
                        </button>

                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + subject.codice, () => {
                                this.deleteSubject(subject);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }
        return (
            <div>
                <div className="row">
                    <BackButton location="xmp"/>
                    <h2 className="page-title">Subject</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="xmp/Subject/add">Add New Subject</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Subject Code</th>
                                <th>Subject Name</th>
                                <th>Category</th>
                                <th>AFP Category</th>
                                <th>Sub Cat</th>
                                <th>Sub Cat1</th>
                                <th>Sub Cat2</th>
                                <th>Action</th>
                            </tr>
                            </thead>
                            <tbody>
                            {subjects}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}
export default SubjectComponent;


