import React, {Component} from 'react';
import {hashHistory,Link} from "react-router";
import BackButton from "../../BackButtonComponent"
import {client} from "../../configs";
class CompetitionComponent extends Component {
    constructor(props){
        super(props);
        this.deleteCompetition = this.deleteCompetition.bind(this);
        this.fetchCompetitions = this.fetchCompetitions.bind(this);
        this.state = {};
        this.fetchCompetitions(0);

    }
    deleteCompetition(competition) {
        client.deleteCompetition(competition).then(response => {
            this.props.setSuccess(competition.nome_it + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchCompetitions(0);
        }).catch(e => {
            this.props.setError("Couldn't delete Competition");
        });
    }


    fetchCompetitions(page) {
        client.getAllCompetition(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }

    render() {
        let competitions = [];
        if (this.state.content) {
            this.state.content.forEach(competition => {
                /*let loading = (this.state.onTesting && this.state.onTesting.id == ftpAccount.id) ?
                    <img src="ajax_loader.gif" width={15}/> : '';*/
                competitions.push(<tr key={competition.aid}>
                    <td>{competition.nome_it}</td>
                    <td>{competition.subject}</td>
                    <td>{competition.subjectabb}</td>
                    <td>{competition.keyword.split(";").join("; ").toUpperCase()}</td>
                    <td>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('xmp/Competition/edit/' + competition.id);
                        }}>Edit
                        </button>

                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + competition.nome_it, () => {
                                this.deleteCompetition(competition);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }
        return (
            <div>
                <div className="row">
                    <BackButton location="xmp"/>
                    <h2 className="page-title">Competitions</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="xmp/Competition/add">Add New Competitions</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Competition</th>
                                <th>Subject</th>
                                <th>Subject Abbreviation</th>
                                <th>Keywords</th>
                                <th>Action</th>
                            </tr>
                            </thead>
                            <tbody>
                            {competitions}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}
export default CompetitionComponent;


