import React, {Component} from "react";
import SquadreModel from "../../models/SquadreModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddSquadre extends Component {
    constructor(props) {
        super(props);
        this.squadre = new SquadreModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadSquadre = this.loadSquadre.bind(this);
        if (props.routeParams.id) {
            this.updateSquadreId = props.routeParams.id;
            this.loadSquadre(props.routeParams.id);
        }

        this.fetchSubjectGroups = this.fetchSubjectGroups.bind(this);
        this.fetchSubjectGroups();
        this.fetchVenuesGroups = this.fetchVenuesGroups.bind(this);
        this.fetchVenuesGroups();
        this.state = {
            subjectGroups: [],
            venuesGroups: [],
         
        }

    }

    fetchSubjectGroups() {
        client.getAllSubjectGroups(0, 100000).then(response => {
            this.setState({
                subjectGroups: response.content
            });
            if(this.state.subjectGroups.length > 0) {
                this.fetchCompetitionGroups(this.state.subjectGroups[0].subject);
            }
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Subject groups. Please reload the page");
        })
    }

    fetchVenuesGroups() {
        client.getAllVenuesGroups(0, 100000).then(response => {
            this.setState({
                venuesGroups: response.content
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Venues groups. Please reload the page");
        })
    }

    loadSquadre(id) {
        client.getSquadreById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];

                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('squadre');
        })
    }

    resetValues() {
        Object.keys(this.squadre).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {

        Object.keys(this.squadre).forEach(key => {
            this.squadre[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
        });

        console.log(this.squadre);
        if (this.updateSquadreId) {
            this.squadre["id"] = this.updateSquadreId;
        }
        client.createSquadre(this.squadre).then(response => {
            this.props.setSuccess("Squadre saved successfully");
            // this.resetValues();
            hashHistory.push("xmp/Squadre");
        }).catch(e => {
            console.error(e);
            e.response.json().then(response => {
                console.log(response);
                this.props.setError(response.error);
            })
        });

        e.preventDefault();
    }
     handler() {
        const elt = document.getElementById('test');
        elt.value = elt.value.toUpperCase();
    };


    render() {
        let subjectOptions = [];
        this.state.subjectGroups.forEach(sub => {
            subjectOptions.push(
                <option key={sub.aid} value={sub.subject}>{sub.subject}</option>
            )
        })
        let venuesOptions = [];
        this.state.venuesGroups.forEach(ven => {
            venuesOptions.push(
                <option key={ven.id} value={ven.venue_name}>{ven.venue_name}</option>
            )
        })
        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">New Teams</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Team Name</label>
                            <input type="text" ref="nome_it" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Team Abbreviation</label>
                            <input type="text" ref="abb_nome" className="form-control" id="test"  onKeyUp={this.handler}  />
                        </div>
                        <div className="form-group">
                            <label>Subject</label>
                            <select ref="subj" className="form-control">
                                {subjectOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Venue</label>
                            <select ref="home_venue" className="form-control">
                                {venuesOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary">Save</button>
                            <button className="btn btn-danger ml-1" type="reset">Reset</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}





export default AddSquadre;




