import React, {Component} from "react";
import AgencyModel from "../../models/AgencyModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddAgenziaComponent extends Component {
    constructor(props) {
        super(props);
        this.agency = new AgencyModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadAgency = this.loadAgency.bind(this);  
        this.validateForm= this.validateForm.bind(this);
        this.formHeader="Add new Agency" 
         if (props.routeParams.id) {
            this.updateAgencyId = props.routeParams.id;
            this.loadAgency(props.routeParams.id);
             this.formHeader="Edit Agency" 
        }             
    }
    
    loadAgency(id) {
        client.getAgencyById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];
                   
                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('agency');
        })
    }

    resetValues() {
        Object.keys(this.agency).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }
    validateForm(){
         var agencyName= ReactDOM.findDOMNode(this.refs["nome_agenzia"]).value.trim();
         if(agencyName==""||agencyName==undefined){
              throw new DOMException("Enter valid agency name");
         }
             var address= ReactDOM.findDOMNode(this.refs["indirizzo"]).value.trim();
         if(address==""||address==undefined){
              throw new DOMException("Enter valid agency address");
         }
              var city= ReactDOM.findDOMNode(this.refs["provincia"]).value.trim();
         if(city==""||city==undefined){
              throw new DOMException("Enter valid agency city");
         }
              var state= ReactDOM.findDOMNode(this.refs["citta"]).value.trim();
         if(state==""||state==undefined){
              throw new DOMException("Enter valid agency state");
         }
        var zip= ReactDOM.findDOMNode(this.refs["cap"]).value.trim();
        var zipvalid =   zip.match(/^[0-9\s\-()+\.]+$/);
                        if(!zipvalid){
                            throw new DOMException("Enter valid zip");
                        } 
         var contry= ReactDOM.findDOMNode(this.refs["nazione"]).value.trim();
         if(contry==""||contry==undefined){
              throw new DOMException("Enter valid agency contry");
         }
        var telefono= ReactDOM.findDOMNode(this.refs["telefono"]).value.trim();
        var telefonovalid =   telefono.match(/^[0-9\s\-()+\.]+$/);
                        if(!telefonovalid){
                            throw new DOMException("Enter valid phone");
                        } 
        
            
        var email = ReactDOM.findDOMNode(this.refs["email"]).value.trim();
                         var emailValid =   email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i);
                        if(!emailValid){
                            throw new DOMException("Enter valid email");
                        } 
        var Website= ReactDOM.findDOMNode(this.refs["sito"]).value.trim();
         if(Website==""||Website==undefined){
              throw new DOMException("Enter valid agency site");
         }
                    
    }
    handleSubmit(e) {
        try{
           this.validateForm();
                Object.keys(this.agency).forEach(key => {  
                     this.agency[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();  
                   
                              
                });
                 
        
                console.log(this.agency);
                if (this.updateAgencyId) {
                this.agency["aid"] = this.updateAgencyId;
                }
                client.createAgency(this.agency).then(response => {
                     if (this.updateAgencyId) {
                    this.props.setSuccess("Agency updated successfully");
                     }else{
                          this.props.setSuccess("Agency saved successfully");
                     }
                   // this.resetValues();
                    hashHistory.push("xmp/Agency");
                }).catch(e => {
                    console.error(e);
                    e.response.json().then(response => {
                        console.log(response);
                        this.props.setError(response.error);
                    })
                });
        }catch(e){
             console.error(e);
              this.props.setError(e.message);
        }
                e.preventDefault();
     }

    render() {
        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title-latest ">{this.formHeader}</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Agency Name</label>
                            <input type="text" ref="nome_agenzia" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Address</label>
                            <input type="text" ref="indirizzo" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>City</label>
                            <input type="text" ref="provincia" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>State</label>
                            <input type="text" ref="citta" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Zip</label>
                            <input type="text" ref="cap" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Country</label>
                            <input type="text" ref="nazione" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Phone</label>
                            <input type="text" ref="telefono" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>E-mail</label>
                            <input type="text" ref="email"  className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Web Site</label>
                            <input type="text" ref="sito" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary">Save</button>
                            <button className="btn btn-danger ml-1" type="reset">Reset</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default AddAgenziaComponent;



