import React, {Component} from "react";
import FtpModel from "../../models/FtpModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddFtpComponent extends Component {
    constructor(props) {
        super(props);
        this.ftp = new FtpModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadFtp = this.loadFtp.bind(this);

        if (props.routeParams.id) {
            this.updateFtpId = props.routeParams.id;
            this.loadFtp(props.routeParams.id);
        }
    }

    loadFtp(id) {
        client.getFtpById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];
                    if (ReactDOM.findDOMNode(this.refs[key]).type == 'checkbox') {
                        ReactDOM.findDOMNode(this.refs[key]).checked = response[key];
                    }
                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('ftp');
        })
    }

    resetValues() {
        Object.keys(this.ftp).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {
        Object.keys(this.ftp).forEach(key => {
            if (ReactDOM.findDOMNode(this.refs[key]).type == 'checkbox') {
                this.ftp[key] = ReactDOM.findDOMNode(this.refs[key]).checked;
            } else {
                this.ftp[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
            }
        });

        if (this.updateFtpId) {
            this.ftp["id"] = this.updateFtpId;
        }

        client.createFtp(this.ftp).then(response => {
            this.props.setSuccess("Ftp destination saved successfully");
            //this.resetValues();
            hashHistory.push("ftp");
        }).catch(e => {
            this.props.setError("error occurred while saving ftp. Please retry");
        });

        e.preventDefault();
    }

    render() {
        return (
            <div>
                <div className="row">
                    <BackButton location="ftp"/>
                    <h2 className="page-title">New FTP Destination</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Name</label>
                            <input type="text" ref="name" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Host</label>
                            <input type="text" ref="host" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Directory</label>
                            <input type="text" ref="directory" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Create Sub directory with event name</label>
                            <input type="checkbox" ref="createFolder" onChange={(event) => {
                                event.target.value = event.target.checked;
                            }} className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>IF SFTP CONNECTION</label>
                            <input type="checkbox" ref="sftpEnabled" onChange={(event) => {
                                event.target.value = event.target.checked;
                            }} className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Username</label>
                            <input type="text" ref="username" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Password</label>
                            <input type="text" ref="password" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Description</label>
                            <textarea className="form-control" ref="description"/>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary">Save</button>
                            <button className="btn btn-danger ml-1" type="reset">Reset</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default AddFtpComponent;


