import React, {Component} from 'react';
import {hashHistory,Link} from "react-router";
import BackButton from "../../BackButtonComponent";
import {client} from "../../configs";

class VenuesComponent extends Component {
    constructor(props){
        super(props);
        this.deleteVenues = this.deleteVenues.bind(this);
        this.fetchVenue = this.fetchVenue.bind(this);
        this.state = {};
        this.fetchVenue(0);

    }
    deleteVenues(venues) {
        client.deleteVenues(venues).then(response => {
            this.props.setSuccess(venues.city + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchVenue(0);
        }).catch(e => {
            this.props.setError("Couldn't delete Venues");
        });
    }


    fetchVenue(page) {
        client.getAllVenues(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }

    render() {
        let venue = [];
        let preButtons;
        let nxtButtons;
        if (this.state.content) {
            this.state.content.forEach(venues => {
                /*let loading = (this.state.onTesting && this.state.onTesting.id == ftpAccount.id) ?
                    <img src="ajax_loader.gif" width={15}/> : '';*/
                venue.push(<tr key={venues.id}>
                    <td>{venues.venue_name}</td>
                    <td>{venues.city}</td>
                    <td>{venues.state}</td>
                    <td>{venues.nation}</td>
                    <td>{venues.iso}</td>
                    <td>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('xmp/Venues/edit/' + venues.id);
                        }}>Edit
                        </button>

                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + venues.city, () => {
                                this.deleteVenues(venues);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
            if (this.state.number && this.state.number != 0) {
                preButtons = <button className="btn btn-info navigation-button"
                                     onClick={() => {
                                         this.fetchVenue(this.state.number - 1)
                                     }}>
                    Previous
                </button>;
            }

            if (this.state.last == false) {
                nxtButtons =
                    <button className="btn btn-info navigation-button"
                            onClick={() => {
                                this.fetchVenue(this.state.number + 1)
                            }}>
                        Next
                    </button>;
            }
        }
        return (
            <div>
                <div className="row">
                    <BackButton location="xmp"/>
                    <h2 className="page-title">Venues</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="xmp/Venues/add">Add New Venues</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Venue Name</th>
                                <th>City</th>
                                <th>State</th>
                                <th>Nation</th>
                                <th>ISO</th>
                                <th>Action</th>
                            </tr>
                            </thead>
                            <tbody>
                            {venue}
                            </tbody>
                        </table>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        {preButtons}{nxtButtons}
                    </div>
                </div>
            </div>
        );
    }
}
export default VenuesComponent;


