import React, {Component} from "react";
import CopyrightModel from "../../models/CopyrightModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddCopyright extends Component {
    constructor(props) {
        super(props);
        this.copyright = new CopyrightModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadCopyright = this.loadCopyright.bind(this);
        if (props.routeParams.id) {
            this.updateCopyrightId = props.routeParams.id;
            this.loadCopyright(props.routeParams.id);
        }
    }

    loadCopyright(id) {
        client.getCopyrightById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];

                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('copyright');
        })
    }

    resetValues() {
        Object.keys(this.copyright).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {

        Object.keys(this.copyright).forEach(key => {
            this.copyright[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
        });

        console.log(this.copyright);
        if (this.updateCopyrightId) {
            this.copyright["id"] = this.updateCopyrightId;
        }
        client.createCopyright(this.copyright).then(response => {
            this.props.setSuccess("Copyright saved successfully");
            // this.resetValues();
            hashHistory.push("xmp/Copyright");
        }).catch(e => {
            console.error(e);
            e.response.json().then(response => {
                console.log(response);
                this.props.setError(response.error);
            })
        });

        e.preventDefault();
    }

    render() {
        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">New Copyright</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Instructions</label>
                            <input type="text" ref="instructions" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Testo</label>
                            <input type="text" ref="testo_it" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary">Save</button>
                            <button className="btn btn-danger ml-1" type="reset">Reset</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default AddCopyright;



