import React, {Component} from 'react';
import {hashHistory,Link} from "react-router";
import BackButton from "../../BackButtonComponent"
import {client} from "../../configs";

class FotografiComponent extends Component {
    constructor(props){
        super(props);
        this.deleteFotografi = this.deleteFotografi.bind(this);
        this.fetchFotografis = this.fetchFotografis.bind(this);
        this.state = {};
        this.fetchFotografis(0);

    }
    deleteFotografi(fotografi) {
        client.deleteFotografi(fotografi).then(response => {
            this.props.setSuccess(fotografi.nome_fotografo + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchFotografis(0);
        }).catch(e => {
            this.props.setError("Couldn't delete Fotografi");
        });
    }


    fetchFotografis(page) {
        client.getAllFotografi(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }
    render() {
        let fotografis = [];
        if (this.state.content) {
            this.state.content.forEach(fotografi => {
                /*let loading = (this.state.onTesting && this.state.onTesting.id == ftpAccount.id) ?
                    <img src="ajax_loader.gif" width={15}/> : '';*/
                fotografis.push(<tr key={fotografi.id}>
                    <td>{fotografi.nome_fotografo}</td>
                    <td>{fotografi.iniziali}</td>
                    <td>{fotografi.authorsPosition}</td>
                    <td>{fotografi.email}</td>
                    <td>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('xmp/Fotografi/edit/' + fotografi.id);
                        }}>Edit
                        </button>

                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + fotografi.nome_fotografo, () => {
                                this.deleteFotografi(fotografi);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }
        return (
            <div>
                <div className="row">
                    <BackButton location="xmp"/>
                    <h2 className="page-title">Photographers</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="/xmp/Fotografi/add">Add New Photographers</Link>
            </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Photographer</th>
                                <th>Iniziali</th>
                                <th>Authors Position</th>
                                <th>Photographer E-mail</th>
                                <th>Action</th>
                            </tr>
                            </thead>
                            <tbody>
                            {fotografis}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}
export default FotografiComponent;


