import React, {Component} from "react";
import {Link} from "react-router";
import "./count.css";
import {client} from "./configs";
class CountComponent extends Component {

    constructor(props) {
        super(props);
        this.fetchcount=this.fetchcount.bind(this);
        this.fetchcount();
        this.state = {

        }
    }


    fetchcount() {
        client.getPhotoesForDay().then(response => {
            this.setState({
                dayCount: response
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Photoes Count");
        })

        client.getPhotoesForWeek().then(response => {
            this.setState({
                weekCount: response
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Photoes Count");
        })

        client.getPhotoesForHour().then(response => {
            this.setState({
                hourCount: response
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Photoes Count");
        })

        client.getPhotoesForTotal().then(response => {
            this.setState({
                totalCount: response
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Photoes Count");
        })
    }
    render() {
        return (

            <div  className="countBox">

                <div className="textBox">
                    <div className="countText"><strong>Picture Processed</strong></div>
                    <div className="countText">Last Hour : {this.state.hourCount}</div>
                    <div className="countText">Last Day : {this.state.dayCount}</div>
                    <div className="countText">Last Week : {this.state.weekCount}</div>
                    <div className="countText">Total : {this.state.totalCount}</div>
                </div>

            </div>

        );
    }
}

export default CountComponent;


