import React, {Component} from "react";
import XmpgeneratorModel from "../../models/XmpgeneratorModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";

import {hashHistory} from "react-router";
import moment from "moment";

class XmpcaptionComponent extends Component {
    constructor(props) {
        super(props);
        this.event = new XmpgeneratorModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.changeSubject = this.changeSubject.bind(this);
        this.changeHomeTeam = this.changeHomeTeam.bind(this);
        this.fetchAgencyGroups = this.fetchAgencyGroups.bind(this);
        this.orderVenues = this.orderVenues.bind(this);
        this.changeVenue = this.changeVenue.bind(this);
        this.changeCompetition=this.changeCompetition.bind(this);


        this.fetchFtpGroups = this.fetchFtpGroups.bind(this);
        this.fetchSquadreGroups = this.fetchSquadreGroups.bind(this);


        this.fetchFotografiGroups = this.fetchFotografiGroups.bind(this);
        this.fetchSubjectGroups = this.fetchSubjectGroups.bind(this);
        this.fetchCompetitionGroups = this.fetchCompetitionGroups.bind(this);
        this.fetchVenuesGroups = this.fetchVenuesGroups.bind(this);
        this.fetchCopyrightGroups = this.fetchCopyrightGroups.bind(this);

        this.fetchAgencyGroups();
        this.fetchFtpGroups();
        this.fetchFotografiGroups();
        this.fetchSquadreGroups();
        this.fetchSubjectGroups();
        //this.fetchVenuesGroups();
        this.fetchCopyrightGroups();


        this.state = {
            agencyGroups: [],
            ftpGroups: [],
            fotografiGroups: [],
            subjectGroups: [],
            competitionGroups: [],
            squadreGroups: [],
            venuesGroups: [],
            copyrightGroups: [],
            defaultVenue: "1"
        }
        this.keys = ["destination", "source", "creator", "subject", "competition", "home", "away", "venue", "eventDate", "instructions"];
    }

    fetchAgencyGroups() {
        client.getAllAgencyGroups(0, 100000).then(response => {
            this.setState({
                agencyGroups: response.content
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Agency groups. Please reload the page");
        })
    }

    fetchFotografiGroups() {
        client.getAllFotografiGroups(0, 100000).then(response => {
            this.setState({
                fotografiGroups: response.content
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Fotografi groups. Please reload the page");
        })
    }

    fetchSubjectGroups() {
        client.getAllSubjectGroups(0, 100000).then(response => {
            this.setState({
                subjectGroups: response.content
            });
            if(this.state.subjectGroups.length > 0) {
                this.fetchCompetitionGroups(this.state.subjectGroups[0].subject);
            }
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Subject groups. Please reload the page");
        })
    }

    fetchCompetitionGroups(subject) {
        client.getCopyrightGroupsBySubject(subject).then(response => {
            this.setState({
                competitionGroups: response
            });
            //fetch Squadre of compittion
            if(this.state.competitionGroups.length > 0) {
                client.getAllCompetitionSquadre(this.state.competitionGroups[0].id).then(response => {
                    this.setState({
                        squadreGroups: response
                    });
                }).catch(e => {
                    console.error(e);
                    this.props.setError("Error in fetching Squadre");
                })
            }


        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Competition groups. Please reload the page");
        })
    }

    changeCompetition(event){
        client.getAllCompetitionSquadre(event.target.value).then(response => {

            this.setState({
                squadreGroups: response
            });
            var homeTeam = this.state.squadreGroups[0];
            this.orderVenues(homeTeam.home_venue);
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Squadre");
        })
    }
    fetchFtpGroups() {
        client.getAllFtpGroups(0, 100000).then(response => {
            this.setState({
                ftpGroups: response.content
            });
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching FTP groups. Please reload the page");
        })
    }

    fetchSquadreGroups() {
        client.getAllSquadreGroups(0, 100000).then(response => {
            this.setState({
                squadreGroups: response.content
            });
            this.fetchVenuesGroups();
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Squadre groups. Please reload the page");
        })
    }

    fetchVenuesGroups() {
        client.getAllVenuesGroups(0, 100000).then(response => {
            this.setState({
                venuesGroups: response.content
            });
            this.orderVenues(this.state.squadreGroups[0].home_venue);
        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Venues groups. Please reload the page");
        })
    }

    orderVenues(homeTeamVenue) {
        for (var i = 0; i < this.state.venuesGroups.length; i++) {
            if (this.state.venuesGroups[i].venue_name === homeTeamVenue) {
                this.setState({
                  defaultVenue: this.state.venuesGroups[i].id.toString()
                });
                break;
            }
        }
    }

    fetchCopyrightGroups() {
        client.getAllCopyrightGroups(0, 100000).then(response => {
            this.setState({
                copyrightGroups: response.content
            });

        }).catch(e => {
            console.error(e);
            this.props.setError("Error in fetching Copyright groups. Please reload the page");
        })
    }

    resetValues() {
        Object.keys(this.event).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {

        this.keys.forEach(key =>{
            if(key === 'eventDate'){
                var date = this.refs[key].value.trim();
                if(date && date != ""){
                    this.event[key] = moment(date).format('DD MMMM YYYY');
                }
            } else if(key === 'destination' || key === 'source' || key === 'creator'|| key === 'subject'|| key === 'venue' || key === 'competition' || key === 'home' || key === 'away'){
                var id = parseInt(this.refs[key].value.trim());
                var obj;
                var value;
                if(key === 'destination') {
                    obj = this.state.ftpGroups.find(x => x.id === id);
                    value = obj.name;
                } else if(key === 'source') {
                    obj = this.state.agencyGroups.find(x => x.aid === id);
                    value = obj.nome_agenzia;
                    this.event['source'].email = obj.email;
                } else if(key === 'creator') {
                    obj = this.state.fotografiGroups.find(x => x.id === id);
                    value = obj.nome_fotografo;
                    this.event['creator'].iniziali = obj.iniziali;
                } else if(key === 'subject') {
                    obj = this.state.subjectGroups.find(x => x.aid === id);
                    value = obj.subject;
                    this.event['subject'].afpcat = obj.afpcat;
                } else if(key === 'competition') {
                    obj = this.state.competitionGroups.find(x => x.id === id);
                    value = obj.nome_it;
                    this.event['competition'].subjectabb = obj.subjectabb;
                    this.event['competition'].keyword = obj.keyword;
                } else if(key === 'home') {
                    obj = this.state.squadreGroups.find(x => x.id === id);
                    value = obj.nome_it;
                    this.event['home'].abb_nome = obj.abb_nome;
                } else if(key === 'away') {
                    obj = this.state.squadreGroups.find(x => x.id === id);
                    value = obj.nome_it;
                    this.event['away'].abb_nome = obj.abb_nome;
                } else {
                    obj = this.state.venuesGroups.find(x => x.id === id);
                    value = obj.venue_name;
                    this.event['venue'].iso = obj.iso;
                    this.event['venue'].city = obj.city;
                    this.event['venue'].nation = obj.nation;
                }
                this.event[key].id = id;
                this.event[key].name = value;
            } else {
                this.event[key] = this.refs[key].value.trim();
            }
        });
        console.log(this.event);
        var data = JSON.stringify(this.event);
        hashHistory.push({pathname: 'xmp/Xmpgenerator1', query: {data}});

        e.preventDefault();
    }
    changeSubject(event){
        var subject = this.state.subjectGroups.find(x => x.aid === parseInt(event.target.value));
        this.fetchCompetitionGroups(subject.subject);
    }
    changeHomeTeam(event){
        var homeTeam = this.state.squadreGroups.find(x => x.id === parseInt(event.target.value));
        this.orderVenues(homeTeam.home_venue);
    }
    changeVenue(event){
        var venue = this.state.venuesGroups.find(x => x.id === parseInt(event.target.value));
        this.setState({
            defaultVenue: venue.id.toString()
        });
    }

    render() {

        let agencyOptions = [];
        this.state.agencyGroups.forEach(fot => {
            agencyOptions.push(
                <option key={fot.aid} value={fot.aid}>{fot.nome_agenzia}</option>
            )
        })

        let groupOptions = [];
        this.state.ftpGroups.forEach(grp => {
            groupOptions.push(
                <option key={grp.id} value={grp.id}>{grp.name}</option>
            )
        })

        let fotografiOptions = [];
        this.state.fotografiGroups.forEach(fot => {
            fotografiOptions.push(
                <option value={fot.id} key={fot.id}>{fot.nome_fotografo}</option>
            )
        })

        let subjectOptions = [];
        this.state.subjectGroups.forEach(sub => {
            subjectOptions.push(
                <option key={sub.aid} value={sub.aid}>{sub.subject}</option>
            )
        })

         let competitionOptions = [];

        this.state.competitionGroups.forEach(com => {
            competitionOptions.push(
                <option key={com.id} value={com.id}>{com.nome_it}</option>
            )
        })

        let squadreOptions = [];
        this.state.squadreGroups.forEach(squ => {
            squadreOptions.push(
                <option key={squ.id} value={squ.id}>{squ.nome_it}</option>
            )
        })

        let venuesOptions = [];
        this.state.venuesGroups.forEach(ven => {
            venuesOptions.push(
                <option key={ven.id} value={ven.id}>{ven.venue_name}</option>
            )
        })

        let copyrightOptions = [];
        this.state.copyrightGroups.forEach(cop => {
            copyrightOptions.push(
                <option key={cop.id} value={cop.testo_it}>{cop.instructions}</option>
            )
        })

        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">New XMP</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Agency</label>
                            <select ref="source" className="form-control">
                                <option selected disabled> Select... </option>
                                {agencyOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Photographer</label>
                            <select ref="creator" className="form-control">
                                <option selected disabled> Select... </option>
                                {fotografiOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Subject</label>
                            <select ref="subject" className="form-control" onChange={this.changeSubject}>
                                <option selected disabled> Select...</option>
                                {subjectOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Competition</label>
                            <select ref="competition" className="form-control" onChange={this.changeCompetition}>
                                <option selected disabled> Select...</option>
                                {competitionOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Home</label>

                            <select ref="home" className="form-control" onChange={this.changeHomeTeam}>
                                <option selected disabled> Select...</option>
                                {squadreOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Away</label>
                            <select ref="away" className="form-control">
                                <option selected disabled> Select...</option>
                                {squadreOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Venue</label>
                            <select ref="venue" className="form-control" value={this.state.defaultVenue} onChange={this.changeVenue}>
                                <option selected disabled> Select...</option>
                            {venuesOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Event Date</label>
                            <input type="date" ref="eventDate" className="form-control" required/>
                        </div>
                        <div className="form-group">
                            <label>Instructions</label>
                            <select ref="instructions" className="form-control">
                                <option selected disabled> Select... </option>
                                {copyrightOptions}
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Distribution List</label>
                            <select ref="destination" className="form-control">
                                <option selected disabled> Select... </option>
                                {groupOptions}
                            </select>
                        </div>

                        <div className="form-group">
                            <button className="btn btn-secondary btn-sm">Next</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}


export default XmpcaptionComponent;



