import React, {Component} from "react";
import {hashHistory, Link} from "react-router";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";

class EmailComponent extends Component {
    constructor(props) {
        super(props);
        this.fetchAccounts = this.fetchAccounts.bind(this);
        this.deleteAccount = this.deleteAccount.bind(this);
        this.fetchAccounts(0);
        this.state = {};
    }

    deleteAccount(ftpAccount) {
        client.deleteFtp(ftpAccount).then(response => {
            this.props.setSuccess(ftpAccount.name + " deleted from the system. But it will be still visible in queue for older entries.");
            this.setState({
                content: null
            });
            this.fetchAccounts(0);
        }).catch(e => {
            this.props.setError("Couldn't delete ftp account");
        });
    }


    fetchAccounts(page) {
        client.getAllEmail(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }

    render() {
        let accounts = [];
        if (this.state.content) {
            this.state.content.forEach(ftpAccount => {
                accounts.push(<tr key={ftpAccount.id}>
                    <td>{ftpAccount.id}</td>
                    <td>{ftpAccount.name}</td>
                    <td>{ftpAccount.email}</td>
                    <td>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('email/edit/' + ftpAccount.id);
                        }}>Edit
                        </button>

                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + ftpAccount.name, () => {
                                this.deleteAccount(ftpAccount);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }

        return (
            <div>
                <div className="row">
                    <BackButton location="/"/>
                    <h2 className="page-title">Email Manager</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="/email/add">Add New Email</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>Email</th>
                                <th>Actions</th>
                            </tr>
                            </thead>
                            <tbody>
                            {accounts}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}

export default EmailComponent;


