import React, {Component} from "react";
import {hashHistory, Link} from "react-router";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";

class FtpComponent extends Component {
    constructor(props) {
        super(props);
        this.fetchAccounts = this.fetchAccounts.bind(this);
        this.deleteAccount = this.deleteAccount.bind(this);
        this.fetchAccounts(0);
        this.state = {};
    }

    deleteAccount(ftpAccount) {
        client.deleteFtp(ftpAccount).then(response => {
            this.props.setSuccess(ftpAccount.name + " deleted from the system. But it will be still visible in queue for older entries.");
            this.setState({
                content: null
            });
            this.fetchAccounts(0);
        }).catch(e => {
            this.props.setError("Couldn't delete ftp account");
        });
    }


    fetchAccounts(page) {
        client.getAllFtp(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }

    testAccount(ftpAccount) {
        this.setState(
            {
                onTesting: ftpAccount
            }
        )
        client.testFtpById(ftpAccount.id).then(response => {
            console.log("working");
            this.props.setSuccess(ftpAccount.name + " is working!");
            this.setState({
                onTesting: false
            })
        }).catch(e => {
            console.log(e);
            e.response.json().then(json => {
                this.props.setError(ftpAccount.name + " is not working." + json.error);

            }).catch(e => {
                this.props.setError(ftpAccount.name + " is not working. Couldn't find the issue.");
            })

            this.setState({
                onTesting: false
            })
        })
    }

    render() {
        let accounts = [];
        if (this.state.content) {
            this.state.content.forEach(ftpAccount => {
                let loading = (this.state.onTesting && this.state.onTesting.id == ftpAccount.id) ?
                    <img src="ajax_loader.gif" width={15}/> : '';
                accounts.push(<tr key={ftpAccount.id}>
                    <td>{ftpAccount.id}</td>
                    <td>{ftpAccount.name}</td>
                    <td>{ftpAccount.directory}</td>
                    <td>{ftpAccount.createFolder?'Yes':'No'}</td>
                    <td>{ftpAccount.sftpEnabled?'Yes':'No'}</td>
                    <td>{ftpAccount.host}</td>
                    <td>{ftpAccount.username}</td>
                    {/*<td>{ftpAccount.email}</td>*/}
                    <td width="300px">
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('ftp/edit/' + ftpAccount.id);
                        }}>Edit
                        </button>
                        <button className="btn btn-primary btn-sm ml-1" onClick={() => {
                            this.testAccount(ftpAccount);
                        }}>{loading} Test
                        </button>
                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + ftpAccount.name, () => {
                                this.deleteAccount(ftpAccount);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }

        return (
            <div>
                <div className="row">
                    <BackButton location="/"/>
                    <h2 className="page-title">FTP Manager</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="/ftp/add">Add New FTP</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>Sub directory</th>
                                <th>Folder Create</th>
                                <th>SFTP</th>
                                <th>Host</th>
                                <th>username</th>
                                {/*<th>Email</th>*/}
                                <th>Actions</th>
                            </tr>
                            </thead>
                            <tbody>
                            {accounts}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}

export default FtpComponent;


