import React, {Component} from "react";
import VenuesModel from "../../models/VenuesModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddVenues extends Component {
    constructor(props) {
        super(props);
        this.venues = new VenuesModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadVenues = this.loadVenues.bind(this);
        if (props.routeParams.id) {
            this.updateVenuesId = props.routeParams.id;
            this.loadVenues(props.routeParams.id);
        }
    }

    loadVenues(id) {
        client.getVenuesById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];

                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('Venues');
        })
    }

    resetValues() {
        Object.keys(this.venues).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {

        Object.keys(this.venues).forEach(key => {
            this.venues[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
        });

        console.log(this.venues);
        if (this.updateVenuesId) {
            this.venues["id"] = this.updateVenuesId;
        }
        client.createVenues(this.venues).then(response => {
            this.props.setSuccess("Venues saved successfully");
            // this.resetValues();
            hashHistory.push("xmp/Venues");
        }).catch(e => {
            console.error(e);
            e.response.json().then(response => {
                console.log(response);
                this.props.setError(response.error);
            })
        });

        e.preventDefault();
    }

    render() {
        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">New Venue</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Venue Name</label>
                            <input type="text" ref="venue_name" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>City</label>
                            <input type="text" ref="city" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>State</label>
                            <input type="text" ref="state" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Nation</label>
                            <input type="text" ref="nation" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>ISO</label>
                            <input type="text" ref="iso" className="form-control"/>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary">Save</button>
                            <button className="btn btn-danger ml-1" type="reset">Reset</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default AddVenues;



