import React, {Component} from "react";
import {hashHistory} from 'react-router'

class BackButton extends Component {
    constructor(prop) {
        super(prop);
        this.goBack = this.goBack.bind(this);
        console.log(prop);
        if (prop.location) {
            this.location = prop.location;
        }
    }


    goBack() {
        if (this.location) {
            hashHistory.push(this.location);
        } else {
            window.history.back();
        }
    }


    render() {
        return (
            <div onClick={this.goBack} className="mr-2">
                <img src="Back-2-2-icon.png" width="40"/>
            </div>
        );
    }
}

export default BackButton;



