import React, {Component} from "react";
import BackButton from "../../BackButtonComponent";
import {Draggable, Droppable} from "react-drag-and-drop";
import {client} from "../../configs";
import "./ftp.css";
import * as ReactDOM from "react-dom";
import {hashHistory} from "react-router";

class FTPGroupCreateComponent extends Component {
    constructor(prop) {
        super(prop);
        this.onDrop = this.onDrop.bind(this);
        this.keywordChange = this.keywordChange.bind(this);
        this.search = this.search.bind(this);
        this.getFtpMarkup = this.getFtpMarkup.bind(this);
        this.nameChange = this.nameChange.bind(this);
        this.create = this.create.bind(this);
        this.resetInputs = this.resetInputs.bind(this);
        this.removeFromDropped = this.removeFromDropped.bind(this);
        this.state = {
            keyword: '',
            ftps: [],
            ftpsMap: [],
            dropped: [],
            groupName: ''
        };
        this.search();

        if (prop.routeParams.id) {
            this.updateFtpId = prop.routeParams.id;
            this.loadFtpGroup(prop.routeParams.id);
        }
    }

    loadFtpGroup(id) {
        client.getFtpGroupById(id).then(response => {
            ReactDOM.findDOMNode(this.refs["groupName"]).value = response.name;
            this.setState({
                groupName: response.name,
                dropped: response.ftpAccounts
            })
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('group');
        })
    }

    onDrop(data) {
        let ftpId = parseInt(data.ftp);
        let dropped = this.state.dropped;
        let alreadyIn = false;
        this.state.dropped.forEach(ftp => {
            if (parseInt(ftp.id) === ftpId) {
                alreadyIn = true;
            }
        });

        if (alreadyIn) {
            return;
        }

        dropped.push(this.state.ftpsMap[ftpId]);

        this.setState({
            dropped: dropped
        })

        console.log(data);
    }

    resetInputs() {
        this.setState({
            groupName: "",
            dropped: []
        });
        ReactDOM.findDOMNode(this.refs["groupName"]).value = "";
    }

    create(e) {
        if (this.state.groupName == undefined || this.state.groupName.trim() == "") {
            this.props.setError("Invalid group name");
            return;
        } else if (this.state.dropped == undefined || this.state.dropped.length == 0) {
            this.props.setError("At least one ftp account should be added to form a group");
            return;
        }

        var newGroup = new Object();
        newGroup.name = this.state.groupName;
        newGroup.ftpAccounts = this.state.dropped;
        if (this.updateFtpId) {
            newGroup.id = this.updateFtpId;
        }
        client.createGroup(newGroup).then(response => {
            this.props.setSuccess("Ftp group saved successfully");
            this.resetInputs();
            hashHistory.push('group');
        }).catch(e => {
            this.props.setError("error occurred while saving ftp group. Please retry");
        });
    }

    nameChange(e) {
        this.setState({groupName: e.target.value});
    }

    keywordChange(e) {
        this.setState({keyword: e.target.value});
    }

    search(e) {
        client.findByKeyword(this.state.keyword.trim()).then(
            response => {
                this.setState({ftps: response});
                let ftpsMap = [];
                this.state.ftps.forEach(ftp => {
                    ftpsMap[ftp.id] = ftp;
                });
                this.setState({
                    ftpsMap: ftpsMap
                })
            }
        ).catch(err => {
            console.error("error");
        });
        if (e) {
            e.preventDefault();
        }
    }

    removeFromDropped(ftp) {
        var index = -1;
        var count = 0;
        this.state.dropped.forEach(f => {
            if (f.id == ftp.id) {
                index = count;
            }
            count++;
        });
        this.state.dropped.splice(index, 1);
        this.setState({
            dropped: this.state.dropped
        })
    }
    getImageSource(ftp){
        if(ftp.email){
            return '<img src="FTP-icon.png" width="60"/>';
        }else{
            return '<img src="FTP-icon.png" width="60"/>';
        }
    }

    getFtpMarkup(ftps, removable) {
        let markup = [];
        let img=[];
        img.push(

        )
        ftps.forEach(ftp => {
            let img=[];
            let ftpText=[];
            if(!ftp.email){
                img.push(
                    <img src="FTP-icon.png" width="60"/>
                );
                ftpText.push(
                    <span className="ftp-address">{ftp.username}@{ftp.host}</span>
                )
            }else{
                img.push(
                    <img src="email-icon.png" width="60"/>
                );
                ftpText.push(
                    <span className="ftp-address">{ftp.email}</span>
                )
            }

            markup.push(
                <Draggable type="ftp" data={ftp.id} key={ftp.id}>
                    <div className="ftp-container">
                        <div className="ftp-icon">
                            {img}
                        </div>
                        <div className="ftp-details">
                            <span className="ftp-name">{ftp.name}</span>
                            <span className="ftp-description">{ftp.description}</span>
                            {ftpText}
                        </div>
                        <div className={"ftp-remove " + (!removable ? "ftp-remove-hidden" : "")} onClick={() => {
                            this.removeFromDropped(ftp);
                        }}>
                            <img src="close_red.png" width="20px"/>
                        </div>
                    </div>
                </Draggable>
            )
        });
        return markup;
    }


    render() {

        let searchResults = [];
        if (this.state.ftps) {
            searchResults = this.getFtpMarkup(this.state.ftps);
        }

        let droppedResults = [];
        if (this.state.dropped) {
            droppedResults = this.getFtpMarkup(this.state.dropped, true);
        }

        return (
            <div>
                <div className="row">
                    <BackButton location="group"/>
                    <h2 className="page-title">New FTP Group</h2>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <form className="form" onSubmit={this.search}>
                            <div className="input-group">
                                <input type="text" placeholder="Search" className="form-control"
                                       onChange={this.keywordChange}/>
                                <span className="input-group-btn">
                                    <button className="btn btn-secondary" type="button"
                                            onClick={this.search}>Go!</button>
                                </span>
                            </div>
                        </form>
                        <div>
                            {searchResults}
                        </div>
                    </div>
                    <div className="col-sm-6">
                        <form className="form">
                            <div className="input-group">
                                <input type="text" placeholder="New group name" className="form-control"
                                       onChange={this.nameChange} ref="groupName"/>
                                <span className="input-group-btn">
                                    <button className="btn btn-primary" type="button"
                                            onClick={this.create}>Save</button>
                                </span>
                            </div>
                        </form>
                        <Droppable
                            types={['ftp']} // <= allowed drop types
                            onDrop={this.onDrop.bind(this)}>
                            <div className="drop-place">
                                Drop FTP accounts here
                            </div>
                            <div className="display-place">
                                {droppedResults}
                            </div>
                        </Droppable>
                    </div>
                </div>
            </div>
        )
    }
}

export  default FTPGroupCreateComponent;


