import React, {Component} from "react";
import {Link} from "react-router";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from 'react-router'

class FtpGroupComponent extends Component {
    constructor(props) {
        super(props);
        this.fetchGroups = this.fetchGroups.bind(this);
        this.fetchGroups(0);
        this.state = {};
    }


    fetchGroups(page) {
        client.getAllFtpGroups(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }

    deleteGroup(ftpGroup) {
        this.props.showPrompt("Do you want to delete " + <ftpGroup className="name"></ftpGroup>, () => {
            client.deleteFtpGroup(ftpGroup).then(response => {
                this.props.setSuccess(ftpGroup.name + " deleted from the system. But it will be still visible in queue for older entries");
                this.setState({
                    content: null
                });
                this.fetchGroups(0);
            }).catch(e => {
                console.log(e);
            })
        }, () => {
            console.log("No Selected for delete");
        });
    }

    render() {
        let accounts = [];
        if (this.state.content) {
            this.state.content.forEach(ftpAccount => {
                accounts.push(<tr key={ftpAccount.id}>
                    <td>{ftpAccount.id}</td>
                    <td>{ftpAccount.name}</td>
                    <td>{ftpAccount.ftpAccounts.length}</td>
                    <td>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('group/edit/' + ftpAccount.id);
                        }}>Edit
                        </button>
                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.deleteGroup(ftpAccount);
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }

        return (
            <div>
                <div className="row">
                    <BackButton location="/"/>
                    <h2 className="page-title">Distribution List</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="/group/add">Create new Group</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>No. of accounts</th>
                                <th>Actions</th>
                            </tr>
                            </thead>
                            <tbody>
                            {accounts}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}

export default FtpGroupComponent;


