import React, {Component} from "react";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {Link} from "react-router";
import Pagination from "react-js-pagination";


class SearchComponent extends Component {
    constructor(props) {
        super(props);
        this.fetchPhotos = this.fetchPhotos.bind(this);
        this.onChange = this.onChange.bind(this);
        this.search = this.search.bind(this);
        this.state = {
            searchCriterion: "",
            activePage: 0,
            totalPages: null,
            itemsCountPerPage:null,
            totalItemsCount:null
        };
        this.fetchPhotos(1);
        this.onChange;
       // this.search;
    }
    onChange(e) {
        this.setState({ searchCriterion: e.target.value });
    }
    search(e) {
        // e.prevendDefault();
        let page = 0;
        client
            .getPhotosByDescriptionAndJobReference(page, this.state.searchCriterion.trim())
            .then((response) => {
                const totalPages = response.totalPages;
                const itemsCountPerPage = response.size;
                const totalItemsCount = response.totalElements;
               
                this.setState({activePage: page})
                this.setState({totalPages: totalPages})
                this.setState({totalItemsCount: totalItemsCount})
                this.setState({itemsCountPerPage: itemsCountPerPage})
                this.setState({results: response.content});
            })
            .catch((err) => {
                console.error(err);
            });
        if (e) {
            e.preventDefault();
        }
    }


    fetchPhotos(page) {
        client.getPhotosByDescriptionAndJobReference(page - 1, this.state.searchCriterion.trim()).then(response => {
            const totalPages = response.totalPages;
            const itemsCountPerPage = response.size;
            const totalItemsCount = response.totalElements;

            this.setState({activePage: page})
            this.setState({totalPages: totalPages})
            this.setState({totalItemsCount: totalItemsCount})
            this.setState({itemsCountPerPage: itemsCountPerPage})
            this.setState({results: response.content});
        }).catch(err => {
            console.error(err);
        })
    }

    render() {
        let rows = [];
        if (this.state.results) {
            this.state.results.forEach(photo => {
                rows.push(<tr key={photo.id}>
                    <td>{photo.id}</td>
                    <Link to={this.props.location.pathname + "/" + photo.id}><img src={client.getThumbnailUrl(photo.fileName)} width="100"/></Link>
                    <td>{photo.fileName}</td>
                    <td>{photo.jobReference}</td>
                    <td>{photo.description}</td>
                    <td>{photo.creator}</td>
                    <td>{new Date(photo.dateAdded).toLocaleDateString()}</td>
                </tr>);
            });

        }

        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title"> Search: {this.state.searchCriterion}</h2>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <form className="form" onSubmit={this.search}>
                            <div className="input-group">
                                <input type="text" placeholder="Search" className="form-control"
                                       value={this.state.searchCriterion}
                                       onChange={this.onChange}/>&nbsp;&nbsp;
                                <span className="input-group-btn">
                                    <button className="btn btn-secondary" type="button" onClick={this.search}>Go!</button>
                                </span>
                            </div>
                        </form>
                        <div>
                            {}
                        </div>
                    </div>
                    <div className="col-sm-12 mt-2">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Id</th>
                                <th>Preview</th>
                                <th>Filename</th>
                                <th>Code</th>
                                <th>Description</th>
                                <th>Photographer</th>
                                <th>Date Added</th>
                            </tr>
                            </thead>
                            <tbody>
                            {rows}
                            </tbody>
                        </table>
                    </div>
                </div>
             
                <div className="d-flex justify-content-center">
                    <Pagination
                    activePage={this.state.activePage}
                    itemsCountPerPage={this.state.itemsCountPerPage}
                    totalItemsCount={this.state.totalItemsCount}
                    pageRangeDisplayed={10}
                    itemClass='page-item'
                    activeClass='active'
                    linkClass='btn btn-light'
                    onChange={this.fetchPhotos}
                    />
                </div>
            </div>
        );
    }
}

export default SearchComponent;


