import React, {Component} from "react";
import {Link} from "react-router";
import "./App.css";
import "../node_modules/bootstrap/dist/css/bootstrap.css";
import "../node_modules/bootstrap/dist/css/bootstrap-grid.css";
import {hashHistory} from "react-router";
import {client} from './configs'

class App extends Component {
    constructor(props) {
        super(props);
        this.setError = this.setError.bind(this);
        this.setSuccess = this.setSuccess.bind(this);
        this.showPrompt = this.showPrompt.bind(this);
        this.doAndHidePrompt = this.doAndHidePrompt.bind(this);
        this.signIn = this.signIn.bind(this);
        this.signOut = this.signOut.bind(this);
        this.state = {
            error: "No error",
            showError: false,
            authorized: false
        }
        this.errorInterval = null;
        this.successInterval = null;
    }

    signIn(token) {
        localStorage.setItem("auth", token);
        client.getUserInfo().then(user => {
            localStorage.setItem("user", JSON.stringify(user));
            localStorage.setItem("authorized", true);
            hashHistory.push("")
        }).catch(e => {
            console.error(e);
            localStorage.setItem("user", null);
            localStorage.setItem("authorized", false);
            hashHistory.push("login");
        })
    }

    signOut() {
        localStorage.setItem("auth", false);
        localStorage.setItem("user", null);
        localStorage.setItem("authorized", false);
        hashHistory.push("login");
    }

    showPrompt(msg, callbackYes, callbackNo) {
        this.setState({
            prompt: {
                show: true,
                msg: msg,
                callbackYes: callbackYes,
                callbackNo: callbackNo
            }
        })
    }

    doAndHidePrompt(callback) {
        this.setState({
            prompt: {
                show: false
            }
        });
        if (callback) {
            console.log(callback);
            callback();
        }
    }

    setSuccess(msg) {
        this.setState({
            success: msg,
            showSuccess: true
        });
        if (this.successInterval) {
            clearInterval(this.successInterval);
        }
        this.successInterval = setInterval(() => {
            this.setState({
                showSuccess: false
            })
        }, 8000);
    }

    setError(error) {
        this.setState({
            error: error,
            showError: true
        });
        if (this.errorInterval) {
            clearInterval(this.errorInterval);
        }
        this.errorInterval = setInterval(() => {
            this.setState({
                showError: false
            })
        }, 8000);
    }

    render() {
        let errorMsg = null;
        if (this.state.showError) {
            errorMsg =
                <div className="notification error">
                    {this.state.error}
                </div>;
        }

        let successMsg = null;
        if (this.state.showSuccess) {
            successMsg = <div className="notification success">
                {this.state.success}
            </div>;
        }

        let prompt = null;
        if (this.state.prompt && this.state.prompt.show) {
            prompt = <div className="message-box-wrapper">
                <div className="message-box">
                    <div className="row">
                        <div className="col-sm-12">
                            <p className="text-center">
                                {this.state.prompt.msg}
                            </p>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-sm-12">
                            <button className="btn btn-danger"
                                    onClick={() => {
                                        this.doAndHidePrompt(this.state.prompt.callbackYes)
                                    }}>Yes
                            </button>
                            <button className="btn btn-primary"
                                    onClick={() => {
                                        this.doAndHidePrompt(this.state.prompt.callbackNo)
                                    }}>No
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        }

        let authorizedButtons;
        if (localStorage.getItem("authorized") === 'true') {
            authorizedButtons = <div>
                <Link to="/">
                    <img src="home.ico" width="30" height="30"/>
                </Link>
                <button className="btn btn-sm btn-warning mx-2" onClick={this.signOut}>logout
                </button>
            </div>
        }

        return (
            <div>
                <nav className="navbar navbar-light bg-faded">
                    <Link className="navbar-brand" to="/">
                        <img src="logo24.png" width="250" height="75"
                             className="d-inline-block align-top" alt=""/>
                    </Link>

                    <div className="navbar-toggler-right my-2 my-lg-0">
                        {authorizedButtons}
                    </div>
                </nav>

                {prompt}
                {errorMsg}
                {successMsg}
                <br/>
                <div className="container">
                    {React.cloneElement(this.props.children, {
                        setError: this.setError,
                        setSuccess: this.setSuccess,
                        goBack: this.goBack,
                        showPrompt: this.showPrompt,
                        signIn: this.signIn,
                        signOut: this.signOut
                    })}
                </div>

            </div>
        );
    }
}

export default App;



