import React, {Component} from "react";
import EmailModel from "../../models/EmailModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddEmailComponent extends Component {
    constructor(props) {
        super(props);
        this.ftp = new EmailModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadFtp = this.loadFtp.bind(this);

        if (props.routeParams.id) {
            this.updateFtpId = props.routeParams.id;
            this.loadFtp(props.routeParams.id);
        }
    }

    loadFtp(id) {
        client.getFtpById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];

                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('email');
        })
    }

    resetValues() {
        Object.keys(this.ftp).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {
        Object.keys(this.ftp).forEach(key => {
            this.ftp[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
        });

        console.log(this.ftp);
        if (this.updateFtpId) {
            this.ftp["id"] = this.updateFtpId;
        }

        client.createFtp(this.ftp).then(response => {
            this.props.setSuccess("Email destination saved successfully");
            //this.resetValues();
            hashHistory.push("email");
        }).catch(e => {
            this.props.setError("error occurred while saving Email. Please retry");
        });

        e.preventDefault();
    }

    render() {
        return (
            <div>
                <div className="row">
                    <BackButton location="email"/>
                    <h2 className="page-title">New Email Destination</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Name</label>
                            <input type="text" ref="name" className="form-control" required/>
                        </div>
                        <div className="form-group">
                            <label>Email</label>
                            <input type="email" className="form-control" ref="email" required/>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary">Save</button>
                            <button className="btn btn-danger ml-1" type="reset">Reset</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default AddEmailComponent;


