import React, {Component} from 'react';
import BackButton from "../../BackButtonComponent";
import {hashHistory,Link} from "react-router";
import {client} from "../../configs";
class CopyrightComponent extends Component {
    constructor(props){
        super(props);
        this.deleteCopyright = this.deleteCopyright.bind(this);
        this.fetchCopyrights = this.fetchCopyrights.bind(this);
        this.state = {};
        this.fetchCopyrights(0);

    }
    deleteCopyright(Copyright) {
        client.deleteCopyright(Copyright).then(response => {
            this.props.setSuccess(Copyright.instructions + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchCopyrights(0);
        }).catch(e => {
            this.props.setError("Couldn't delete Copyright");
        });
    }


    fetchCopyrights(page) {
        client.getAllCopyright(page).then(response => {
            this.setState(response);
        }).catch(e => {
            console.error(e);
        })
    }
    render() {
        let copyrights = [];
        if (this.state.content) {
            this.state.content.forEach(copyright => {
                /*let loading = (this.state.onTesting && this.state.onTesting.id == ftpAccount.id) ?
                    <img src="ajax_loader.gif" width={15}/> : '';*/
                copyrights.push(<tr key={copyright.id}>
                    <td>{copyright.instructions}</td>
                    <td>{copyright.testo_it}</td>
                    <td>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('xmp/Copyright/edit/' + copyright.id);
                        }}>Edit
                        </button>

                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + copyright.instructions, () => {
                                this.deleteCopyright(copyright);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>
                    </td>
                </tr>);
            });
        }
        return (
            <div>
                <div className="row">
                    <BackButton location="xmp"/>
                    <h2 className="page-title">Copyright</h2>
                </div>
                <div className="row">
                    <div className="col-sm-12 text-right">
                        <Link className="btn btn-info" to="xmp/Copyright/add">Add New Copyright</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Instructions</th>
                                <th>Testo</th>
                                <th>Action</th>
                            </tr>
                            </thead>
                            <tbody>
                            {copyrights}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}
export default CopyrightComponent;



