import React, {Component} from "react";
import SubjectModel from "../../models/SubjectModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import {hashHistory} from "react-router";

class AddSubject extends Component {
    constructor(props) {
        super(props);
        this.subject = new SubjectModel();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.loadSubject= this.loadSubject.bind(this);
        if (props.routeParams.id) {
            this.updateSubjectId = props.routeParams.id;
            this.loadSubject(props.routeParams.id);
        }
    }

    loadSubject(id) {
        client.getSubjectById(id).then(response => {
            Object.keys(response).forEach(key => {
                if (this.refs[key]) {
                    ReactDOM.findDOMNode(this.refs[key]).value = response[key];

                }
            });
        }).catch(e => {
            this.props.setError("Update failed. Please retry");
            hashHistory.push('subject');
        })
    }

    resetValues() {
        Object.keys(this.subject).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {

        Object.keys(this.subject).forEach(key => {
            this.subject[key] = ReactDOM.findDOMNode(this.refs[key]).value.trim();
        });

        console.log(this.subject);
        if (this.updateSubjectId) {
            this.subject["aid"] = this.updateSubjectId;
        }
        client.createSubject(this.subject).then(response => {
            this.props.setSuccess("Subject saved successfully");
            // this.resetValues();
            hashHistory.push("xmp/Subject");
        }).catch(e => {
            console.error(e);
            e.response.json().then(response => {
                console.log(response);
                this.props.setError(response.error);
            })
        });

        e.preventDefault();
    }
    handler_afp() {
        let elt = document.getElementById('afp');
        if(elt){
            elt.value = elt.value.toUpperCase();
        }
    };
    handler_cat() {
        let elt = document.getElementById('cat');
        if(elt){
            elt.value = elt.value.toUpperCase();
        }
    };
    handler_sub() {
        let elt = document.getElementById('sub');
        if(elt){
            elt.value = elt.value.toUpperCase();
        }
    };
    handler_sub1() {
        let elt = document.getElementById('sub1');
        if(elt){
            elt.value = elt.value.toUpperCase();
        }
    };
    handler_sub2() {
        let elt = document.getElementById('sub2');
        if(elt){
            elt.value = elt.value.toUpperCase();
        }
    };

render() {
    return (
        <div>
            <div className="row">
                <BackButton/>
                <h2 className="page-title">New Subjects</h2>
            </div>
            <div className="row">
                <form onSubmit={this.handleSubmit}>
                    <div className="form-group">
                        <label>Subject Code</label>
                        <input type="text" ref="codice" className="form-control"/>
                    </div>
                    <div className="form-group">
                        <label>Subject Name</label>
                        <input type="text" ref="subject" className="form-control"/>
                    </div>
                    <div className="form-group">
                        <label>AFP Category</label>
                        <input type="text" ref="afpcat" className="form-control" id="afp"  onKeyUp={this.handler_afp}/>
                    </div>
                    <div className="form-group">
                        <label>Category</label>
                        <input type="text" ref="categoria" className="form-control" id="cat"  onKeyUp={this.handler_cat}/>
                    </div>
                    <div className="form-group">
                        <label>SubCategory</label>
                        <input type="text" ref="subcat" className="form-control" id="sub"  onKeyUp={this.handler_sub}/>
                    </div>
                    <div className="form-group">
                        <label>SubCategory 1</label>
                        <input type="text" ref="subcat1" className="form-control" id="sub1"  onKeyUp={this.handler_sub1}/>
                    </div>
                    <div className="form-group">
                        <label>SubCategory 2</label>
                        <input type="text" ref="subcat2" className="form-control" id="sub2"  onKeyUp={this.handler_sub2}/>
                    </div>
                    <div className="form-group">
                        <button className="btn btn-secondary">Save</button>
                        <button className="btn btn-danger ml-1" type="reset">Reset</button>
                    </div>
                </form>
            </div>
        </div>
    );
}
}





export default AddSubject;



