import React, {Component} from "react";
import IconComponent from "./IconComponent";
import CounterComponent  from "./countComponenet";
import {client} from "./configs";
import {hashHistory} from "react-router";

class DashboardComponent extends Component {

    constructor(props) {
        super(props);
        //client.ping();
    }

    render() {
        let menus = new Array();

        if(localStorage.getItem("authorized") === 'true'){
            let user = JSON.parse(localStorage.getItem("user"));
            if(user != null){
                if(user.authorsPosition == 'Contributer'){
                    hashHistory.push("photographer")
                    return null
                }
            }
        }else{
            hashHistory.push("login")
            return null;
        }

        return (
            <div>
                <div className="row">
                    <IconComponent to="/ftp" title="FTP" ico="ftp_ico.png"/>
                    <IconComponent to="/email" title="Email" ico="Email.png"/>
                    <IconComponent to="/group" title="Groups" ico="networking.ico"/>
                    <IconComponent to="/event" title="Events" ico="events_ico.png"/>
                    <IconComponent to="/queue" title="Queue" ico="process.png"/>
                    <IconComponent to="/search" title="Search" ico="search.png"/>
                    <IconComponent to="/xmp" title="Xmp Creator" ico="soccer-icon.png"/>
                </div>
                <div className="row">
                    <CounterComponent/>
                </div>
            </div>
        )
    }
}

export default DashboardComponent;


