import React, {Component} from "react";
import XmpgeneratorModel from "../../models/XmpgeneratorModel";
import ReactDOM from "react-dom";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import FileSaver from 'file-saver';
import {hashHistory} from "react-router";
import moment from "moment";

class Xmpgenerator1Component extends Component {
    constructor(props) {
        super(props);
        this.event = JSON.parse(this.props.location.query.data);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.resetValues = this.resetValues.bind(this);
        this.state = {}
        this.keys = ["byline", "credit", "headline", "description", "keywords", "copyright", "newInstructions", "title", "filename", "emailCheck"];
    }

    resetValues() {
        Object.keys(this.event).forEach(key => {
            ReactDOM.findDOMNode(this.refs[key]).value = "";
        });
    }

    handleSubmit(e) {

        this.keys.forEach(key =>{
            this.event[key] = this.refs[key].type === 'checkbox' ? this.refs[key].checked : this.refs[key].value.trim();
        });
        console.log(this.event);

        client.createXmpGenerator(this.event).then(response => {
            var date = moment(this.event.eventDate, 'DD MMMM YYYY');
            FileSaver.saveAs(response,"SP24-" + this.event.home.name.split(" ").join("-").toUpperCase() + "-V-" + this.event.away.name.split(" ").join("-").toUpperCase() + "-" + this.event.competition.subjectabb.split(" ").join("-").toUpperCase() + "-" + moment(date).format('DD-MM-YYYY')+".xmp");
            this.props.setSuccess("Event saved successfully");
            this.resetValues();
        }).catch(e => {
            if(e.response.status === 400){
                this.props.setError("This event has already been generated");
            } else {
                this.props.setError("Error in event creation. Please try again later.");
            }
            console.error(e);
        });

        hashHistory.push('xmp/Xmpgenerator');

        e.preventDefault();
    }

    render() {
        var date = moment(this.event.eventDate, 'DD MMMM YYYY');
        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">New XMP</h2>
                </div>
                <div className="row">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <label>Byline</label>
                            <input type="text" ref="byline" className="form-control" defaultValue={this.event.creator.name}/>
                        </div>
                        <div className="form-group">
                            <label>Credit</label>
                            <input type="text" ref="credit" className="form-control" defaultValue={this.event.source.name + "/" + this.event.creator.name}/>
                        </div>
                        <div className="form-group">
                            <label>Hedline</label>
                            <input type="text" ref="headline" className="form-control" defaultValue={this.event.home.name + " v " + this.event.away.name + " - " + this.event.competition.name}/>
                        </div>
                        <div className="form-group">
                            <label>Description</label>
                            <input type="text" ref="description" className="form-control" defaultValue={"during the " +  this.event.competition.name + " match between " + this.event.home.name + " and " + this.event.away.name + " at " + this.event.venue.name + " " + this.event.venue.city + " " + this.event.venue.nation + " on " + this.event.eventDate +"."+" (Photo " + this.event.creator.name +")"}/>
                        </div>
                        <div className="form-group">
                            <label>Keywords</label>
                            <input type="text" ref="keywords" className="form-control" defaultValue={this.event.home.name + ";" + this.event.away.name + ";" + this.event.venue.name + ";" + this.event.competition.name + ";" + this.event.competition.keyword}/>

                        </div>
                        <div className="form-group">
                            <label>Copyright</label>
                            <input type="text" ref="copyright" className="form-control" defaultValue={this.event.source.name + "/" + this.event.creator.name }/>
                        </div>
                        <div className="form-group">
                            <label>Instructions</label>
                            <input type="text" ref="newInstructions" className="form-control" defaultValue={this.event.instructions + " " + this.event.source.name}/>
                        </div>
                        <div className="form-group">
                            <label>File Name</label>
                            <input type="text" ref="filename" className="form-control" defaultValue={"SP24_" + this.event.creator.iniziali + "_" + this.event.competition.subjectabb.split(" ").join("_").toUpperCase() + "_" + this.event.home.abb_nome.split(" ").join("_").toUpperCase() + "-V-" + this.event.away.abb_nome.split(" ").join("_").toUpperCase() + "_" + moment(date).format('DD-MM-YY').split("-").join("")}/>
                        </div>

                        <div className="form-group">
                            <label>Title</label>
                            <input type="text" ref="title" className="form-control" defaultValue={this.event.subject.afpcat + "-" + this.event.venue.iso + "-" + this.event.competition.name.split(" ").join("-").toUpperCase() + "-" + this.event.home.name.split(" ").join("-").toUpperCase() + "-V-" + this.event.away.name.split(" ").join("-").toUpperCase()}/>
                        </div>
                        <div className="form-group">
                            <label>Email to Photographer</label>
                            <input type="checkbox" ref="emailCheck" className="ml-2"/>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-secondary" id="saveBtn">Save</button>
                            <button className="btn btn-danger ml-1" type="reset">Reset</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default Xmpgenerator1Component;



