import React, {Component} from 'react';
class ImageStatusComponent extends Component {
    render() {
        return (
            <div className="col-sm-12">
                <table className="table">
                    <thead>
                    <tr>
                        <td>
                            Destination
                        </td>
                        <td>
                            Status
                        </td>
                        <td>
                            Time
                        </td>
                    </tr>
                    </thead>
                </table>
            </div>
        );
    }
}


export default ImageStatusComponent;




