import React, {Component} from "react";
import {hashHistory, Link} from "react-router";
import {client} from "../../configs";
import BackButton from "../../BackButtonComponent";
import FileSaver from 'file-saver';
import moment from "moment";
import Pagination from "react-js-pagination";

class EventComponent extends Component {
    constructor(props) {
        super(props);
        this.deleteEvent = this.deleteEvent.bind(this);
        this.fetchEvents = this.fetchEvents.bind(this);
        this.onChange = this.onChange.bind(this);
        this.search = this.search.bind(this);
        this.state = {
            searchCriterion: "",
            activePage: 0,
            totalPages: 0,
            itemsCountPerPage:0,
            totalItemsCount:0
        };
        this.fetchEvents(1);
        
      
    }
    deleteEvent(event) {
        client.deleteEvent(event).then(response => {
            this.props.setSuccess(event.name + " deleted from the system. ");
            this.setState({
                content: null
            });
            this.fetchEvents(0);
        }).catch(e => {
            this.props.setError("Couldn't delete Event");
        });
    }

    onChange(e) {
        this.setState({ searchCriterion: e.target.value });
    }

    search(e) {
        // e.prevendDefault();
        let page = 0;
        client
            .getEventsByNameAndCode(page, this.state.searchCriterion.trim())
            .then((response) => {
                const totalPages = response.totalPages;
                const itemsCountPerPage = response.size;
                const totalItemsCount = response.totalElements;
               
                this.setState({activePage: page})
                this.setState({totalPages: totalPages})
                this.setState({totalItemsCount: totalItemsCount})
                this.setState({itemsCountPerPage: itemsCountPerPage})
                this.setState({results: response.content});
            })
            .catch((err) => {
                console.error(err);
            });
        if (e) {
            e.preventDefault();
        }
    }

    fetchEvents(page) {
        client.getEventsByNameAndCode(page - 1, this.state.searchCriterion.trim()).then(response => {
            console.log(response)
            const totalPages = response.totalPages;
            const itemsCountPerPage = response.size;
            const totalItemsCount = response.totalElements;
           
            this.setState({activePage: page})
            this.setState({totalPages: totalPages})
            this.setState({totalItemsCount: totalItemsCount})
            this.setState({itemsCountPerPage: itemsCountPerPage})
            this.setState({results: response.content});
        }).catch(e => {
            console.error(e);
        })
    }
    render() {
        let accounts = [];
        let preButtons;
        let nxtButtons;
        if (this.state.results) {
            this.state.results.forEach(event => {
                accounts.push(<tr key={event.id}>
                    <td>{event.id}</td>
                    <td>{event.name}</td>
                    <td>{event.eventCode}</td>
                    <td>{new Date(event.eventDate).toLocaleDateString()}</td>
                    <td>{event.processedPhotosCount}</td>
                    <td>{event.destination.name}</td>
                    <td>{event.creator}</td>
                    <td width="300px">
                        <Link to={'event' + '/' + event.eventCode}>
                            <button className="btn btn-primary btn-sm" >
                                View Photos
                            </button>
                        </Link>
                        <button className="btn btn-secondary btn-sm" onClick={() => {
                            hashHistory.push('event/edit/' + event.id);
                        }}>Edit
                        </button>
                        <button className="btn btn-danger btn-sm ml-1" onClick={() => {
                            this.props.showPrompt("Do you really want to delete " + event.name, () => {
                                this.deleteEvent(event);
                            }, () => {
                                //alert('no');
                            })
                        }}>Delete
                        </button>

                            <button className="btn btn-primary btn-sm" onClick={()=>{
                                client.downloadXmpFile(event.id).then(response => {
                                    FileSaver.saveAs(response,event.filename+".xmp");
                                    this.props.setSuccess("Event downloaded successfully");
                                    this.resetValues();
                                }).catch(e => {
                                        this.props.setError("Error in Downloading Event Xmp File");
                                    console.error(e);
                                });
                            }} >
                                Xmp
                            </button>

                    </td>
                </tr>);
            });
            if (this.state.number && this.state.number != 0) {
                preButtons = <button className="btn btn-info navigation-button"
                                     onClick={() => {
                                         this.fetchEvents(this.state.number - 1)
                                     }}>
                    Previous
                </button>;
            }

            if (this.state.last == false) {
                nxtButtons =
                    <button className="btn btn-info navigation-button"
                            onClick={() => {
                                this.fetchEvents(this.state.number + 1)
                            }}>
                        Next
                    </button>;
            }
        }



        return (
            <div>
                <div className="row">
                    <BackButton/>
                    <h2 className="page-title">Event Manager</h2>
                </div>
                <div className="row">
                <div className="col-sm-6">
                        <form className="form" onSubmit={this.search}>
                            <div className="input-group">
                                <input type="text" placeholder="Search" className="form-control"
                                       value={this.state.searchCriterion}
                                       onChange={this.onChange}/>&nbsp;&nbsp;
                                <span className="input-group-btn">
                                    <button className="btn btn-secondary" type="button" onClick={this.search}>Go!</button>
                                </span>
                            </div>
                        </form>
                        <div>
                            {}
                        </div>
                    </div>
                    <div className="col-sm-6 text-right">
                        <Link className="btn btn-info" to="xmp/Xmpgenerator">Add New Event</Link>
                    </div>
                </div>
                <br/>
                <div className="row">
                    <div className="col-sm-12">
                        <table className="table table-bordered table-hover">
                            <thead>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>Code</th>
                                <th>Date</th>
                                <th>Pic.Sent</th>
                                <th>Ftp Group</th>
                                <th>Photographer</th>
                                <th>Actions</th>
                            </tr>
                            </thead>
                            <tbody>
                            {accounts}
                            </tbody>
                        </table>
                    </div>
                </div>
                {/* <div className="row">
                    <div className="col-sm-12 text-right">
                        {preButtons}{nxtButtons}
                    </div>
                </div> */}

                <div className="d-flex justify-content-center">
                    <Pagination
                    activePage={this.state.activePage}
                    itemsCountPerPage={this.state.itemsCountPerPage}
                    totalItemsCount={this.state.totalItemsCount}
                    pageRangeDisplayed={10}
                    itemClass='page-item'
                    activeClass='active'
                    linkClass='btn btn-light'
                    onChange={this.fetchEvents}
                    />
                </div>
            </div>
            
        );
    }
}

export default EventComponent;


